import { Action } from '@ngrx/store';
import { RestRequestInfo, RestRequestLink, OwnerRequest, RequestModel } from '../../models';

export enum SavedRequestsActionTypes {
  SAVE_REQUEST = '[Saved requests] save request',
  SAVED_REQUEST = '[Saved requests] saves request',
  SAVE_REQUEST_FAILED = '[Saved requests] save request failed',

  LOAD_REQUESTS = '[Saved requests] load requests',
  LOADED_REQUESTS = '[Saved requests] loaded requests',
  LOAD_REQUESTS_FAILED = '[Saved requests] load requests failed',

  LOAD_REQUEST = '[Saved requests] load request',
  LOADED_REQUEST = '[Saved requests] loaded request',
  LOAD_REQUEST_FAILED = '[Saved requests] load request failed',
  DESELECT_REQUEST = '[Saved requests] deselect request',
  DELETE_REQUEST = '[Saved requests] delete request',
  DELETED_REQUEST = '[Saved requests] deleted request',
}

export class SaveRequest implements Action {
  readonly type = SavedRequestsActionTypes.SAVE_REQUEST;

  constructor(public payload: RestRequestInfo<RequestModel|string>) {}
}

export class SavedRequest implements Action {
  readonly type = SavedRequestsActionTypes.SAVED_REQUEST;

  constructor(public payload: RestRequestLink) {}
}

export class SaveRequestFailed implements Action {
  readonly type = SavedRequestsActionTypes.SAVE_REQUEST_FAILED;

  constructor(public payload: RestRequestInfo) {}
}

export class LoadRequests implements Action {
  readonly type = SavedRequestsActionTypes.LOAD_REQUESTS;
}

export class LoadedRequests implements Action {
  readonly type = SavedRequestsActionTypes.LOADED_REQUESTS;

  constructor(public payload: RestRequestLink[]) {}
}

export class LoadRequestsFailed implements Action {
  readonly type = SavedRequestsActionTypes.LOAD_REQUESTS_FAILED;

  constructor(public payload) {}
}

export class LoadRequest implements Action {
  readonly type = SavedRequestsActionTypes.LOAD_REQUEST;

  constructor(public payload: OwnerRequest) {}
}

export class LoadedRequest implements Action {
  readonly type = SavedRequestsActionTypes.LOADED_REQUEST;

  constructor(public payload: RestRequestInfo) {}
}

export class LoadRequestFailed implements Action {
  readonly type = SavedRequestsActionTypes.LOAD_REQUEST_FAILED;

  constructor(public payload) {}
}

export class DeselectRequest implements Action {
  readonly type = SavedRequestsActionTypes.DESELECT_REQUEST;

  constructor(public payload?: string) {}
}

export class DeleteRequest implements Action {
  readonly type = SavedRequestsActionTypes.DELETE_REQUEST;

  constructor(public payload: number) {}
}

export class DeletedRequest implements Action {
  readonly type = SavedRequestsActionTypes.DELETED_REQUEST;

  constructor(public payload: number) {}
}

export type SavedRequestsActionsUnion = SaveRequest | SavedRequest | SaveRequestFailed |
  LoadRequests | LoadedRequests | LoadRequestsFailed |
  LoadRequest | LoadedRequest | LoadRequestFailed | DeselectRequest | DeleteRequest | DeletedRequest;
