import { WorkflowConfig, UIModel, WorkflowLog } from '@ngx-dynamic-components/core';
import { PageInfo } from 'src/app/shared/models';
import { ScriptContext } from '../rest-interpreter/models';

export interface NameValue {
  name: string;
  value: object|string;
}

export interface ActiveNameValue extends NameValue {
    active: boolean;
}

// export type ContentTypes = 'application/json' | 'application/xml' | 'text/plain';

export enum ContentTypes {
  JSON = 'application/json',
  XML = 'application/xml',
  TEXT = 'text/plain',
  FORM = 'application/x-www-form-urlencoded'
}

export interface WorkflowModel {
    title: string;
    description: string;
    workflowsMap: any|object;
}

/**
 * @deprecated
 * Use instead PageInfo
 */
export interface DynamicPageModel extends WorkflowModel {
    uiModel: UIModel;
}

export interface RequestModel {
    method: string;
    url: string;
    headers: ActiveNameValue[];
    queryParameters: ActiveNameValue[];
    contentType: ContentTypes;
    body?: string;
    cancelRequest?: boolean;
}

export interface ResponseStatus {
  time: number;
  status: number;
  statusText: string;
  date: string;
  error?: string;
}

export interface ResponseModel {
    contentType: ContentTypes;
    body: string | object;
    headers: object;
}

export interface UISettings {
    isRequestPanelCollapsed?: boolean;
    showObjectExplorer?: boolean;
    showContent?: boolean;
    showHeader?: boolean;
    showWorkflow?: boolean;
    showPages?: boolean;
}

/**
 * This is main model/state file for content
 * RestContentModel interface should be loaded/saved from either history, backend or file.
 */
export interface RestContentModel<T = RequestModel> {
  request?: T;
  uiSettings?: UISettings;
  dynamicPages?: PageInfo[];
  scripts?: string;
}

export interface HistoryRestContentModel extends RestContentModel {
  responseInfo: ResponseStatus;
}

export enum LinkType {
  RestRequest = 'RestRequest',
  ScriptedRequest = 'MultiRequest',
  DataEditor = 'JsonValue'
}

export interface RestRequestLink {
    id?: number;
    ownerName: string;
    name: string;
    folder: string;
    description?: string;
    linkType: LinkType;
}

export interface RestRequestInfo<T = RequestModel> extends RestRequestLink, RestContentModel<T> {
}

export interface OwnerRequest {
  ownerName: string;
  requestName: string;
}

export interface SendRequestStatus {
  scriptContext: ScriptContext;
  response?: ResponseModel;
  status?: ResponseStatus;
}
