import { createSelector } from '@ngrx/store';

import { RestClientState } from '../index';
import { SavedRequestState } from '../reducers/saved-requests';

export const selectFeature = (state: RestClientState) => state.savedRequests;

export const selectActiveRequest = createSelector(
  selectFeature,
  (state: SavedRequestState) => state.activeRequest);

export const selectError = createSelector(
  selectFeature,
  (state: SavedRequestState) => state.error);

export const selectRequestLoading = createSelector(
  selectFeature,
  (state: SavedRequestState) => state.requestLoading);
