import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Subject, Observable } from 'rxjs';
import { switchMap, takeUntil } from 'rxjs/operators';
import { DialogService } from 'src/app/shared/services';
import { ContentExplorerComponent } from 'src/app/shared';
import { LoginService, AuthService } from 'src/app/security/services';
import { SaveRequestComponent } from '../save-request/save-request.component';
import { SaveRequest, DeleteRequest, UpdateRequestModel } from '../../store/actions';
import { selectActiveRequest, selectRequestLoading } from '../../store/selectors';
import { RestClientState, selectRequestModel } from '../../store';
import { RestRequestLink, LinkType } from '../../models';

@Component({
  selector: 'app-data-editor',
  templateUrl: './data-editor.component.html',
  styleUrls: ['./data-editor.component.scss']
})
export class DataEditorComponent implements OnInit, OnDestroy {

  @ViewChild('dataEditor', { static: true }) dataEditor: ContentExplorerComponent;
  isAuthenticated = false;
  saveRequestLink: RestRequestLink;
  selectedSavedRequestLink: RestRequestLink;
  data: object;
  destroy$ = new Subject();
  loading$: Observable<any>;
  private newRequestLink: RestRequestLink = {
    ownerName: null,
    name: null,
    folder: null,
    description: null,
    linkType: LinkType.DataEditor,
    id: 0
  };

  constructor(
    private store: Store<RestClientState>,
    private dialogService: DialogService,
    private loginService: LoginService,
    private authService: AuthService,
    private router: Router) { }

  ngOnInit() {
    this.store.dispatch(new UpdateRequestModel(null));
    this.authService.isAuthenticated$.subscribe(isAuth => {
      this.isAuthenticated = isAuth;
      if (isAuth) {
        const userName = this.authService.user.userName;
        this.newRequestLink.ownerName = userName;
        if (this.saveRequestLink && this.saveRequestLink.id === 0) {
          this.saveRequestLink.ownerName = userName;
        }
      }
    });

    this.store.select(selectActiveRequest).pipe(takeUntil(this.destroy$)).subscribe(request => {
      this.data = null;
      this.selectedSavedRequestLink = request;
      this.saveRequestLink = request || { ...this.newRequestLink };
    });

    this.store.select(selectRequestModel).pipe(takeUntil(this.destroy$)).subscribe(model => {
      if (this.saveRequestLink.linkType === LinkType.DataEditor) {
        this.data = model;
      }
    });

    this.loading$ = this.store.select(selectRequestLoading);
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  saveJson() {
    if (this.isAuthenticated) {
      const saveDialog = this.dialogService.open(SaveRequestComponent);
      saveDialog.config = this.saveRequestLink;
      saveDialog.init.pipe(
        switchMap(() => saveDialog.component.save)).subscribe(data => {
          this.store.dispatch(new SaveRequest({
            ...data as RestRequestLink,
            request: (this.dataEditor.dataModel as string)
          }));
          saveDialog.component.modalRef.hide();
        });
    } else {
      this.loginService.openDialog();
    }
  }

  onDeleteRequest() {
    if (this.selectedSavedRequestLink) {
      this.store.dispatch(new DeleteRequest(this.selectedSavedRequestLink.id));
    } else {
      throw new Error('Saved request is not selected');
    }
  }

  onNewDataEditor() {
    this.store.dispatch(new UpdateRequestModel(null));
    this.router.navigate(['/rest-client', 'data-editor']);
  }

}
