import { HistoryRestContentModel, RestContentModel, ResponseStatus } from './models';
import { getScriptName } from './utils';

export class RequestsHistoryManager {

  private static callbacks: ((...args: any[]) => any)[] = [];

  static getSavedRequests(): HistoryRestContentModel[] {
    return Object.values(getRequestsMap())
      .reduce((prev: any, val: HistoryRestContentModel) => [...prev, val], []);
  }

  static saveRequestConfig(restContentModel: HistoryRestContentModel) {
    try {
      const requests = getRequestsMap();
      if (!restContentModel.responseInfo) {
        restContentModel.responseInfo = RequestsHistoryManager.getInitResponseInfo();
      }
      const key = RequestsHistoryManager.getKey(restContentModel);
      if (key) {
        const isNew = !requests.hasOwnProperty(key);
        requests[key] = restContentModel;
        localStorage.setItem('requests', JSON.stringify(requests));
        RequestsHistoryManager.callbacks.forEach(c => c(restContentModel, isNew));
      }
    } catch (e) {
      console.log(e);
    }
  }

  static updateRequestConfig(restContentModel: RestContentModel) {
    const requests = getRequestsMap();
    const historyModel: HistoryRestContentModel = {
      ...restContentModel,
      responseInfo: RequestsHistoryManager.getInitResponseInfo()
    };
    const requestToUpdate = requests[RequestsHistoryManager.getKey(historyModel)] as HistoryRestContentModel;
    if (requestToUpdate) {
      RequestsHistoryManager.saveRequestConfig(Object.assign(requestToUpdate, restContentModel));
    } else {
      // throw new Error('Request configuration changed and should be send before update');
    }
  }

  static getInitResponseInfo() {
    return {
        date: new Date().toDateString(),
        status: 0,
        statusText: null,
        time: null
    };
  }

  static onRequestSave(callback: (...args: any[]) => any) {
    if (typeof callback === 'function') {
      RequestsHistoryManager.callbacks.push(callback);
    }
  }

  static equals(reqA: HistoryRestContentModel, reqB: HistoryRestContentModel): boolean {
    return RequestsHistoryManager.getKey(reqA) === RequestsHistoryManager.getKey(reqB);
  }

  static getKey(value: HistoryRestContentModel): string {
    if (!value) {
      return null;
    }
    const date = new Date(value.responseInfo.date).toDateString();
    if (value.request) {
      const body = JSON.stringify(value.request.body);
      const headers = JSON.stringify(value.request.headers);
      const queryParams =  JSON.stringify(value.request.queryParameters);
      return [date, value.request.method, value.request.url, body, headers, queryParams].
        map(v => v && v.replace(/ /g, '')).join('-');
    } else {
      const name = getScriptName(value.scripts);
      if (name) {
        return [date, name].map(v => v && v.replace(/ /g, '')).join('-');
      }
    }
  }
}

function getRequestsMap() {
  const requestsStr = localStorage.getItem('requests');
  let requests: {[key: string]: HistoryRestContentModel};

  if (requestsStr) {
    requests = JSON.parse(requestsStr);
  } else {
    requests = {};
  }
  return requests;
}
