import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthService } from 'src/app/security';
import { Store } from '@ngrx/store';
import { selectError, selectActiveRequest } from '../../store/selectors/saved-requests';
import { RestClientState } from '../../store';
import { TabsetComponent } from 'ngx-bootstrap';
import { LoginService } from 'src/app/security/services/login.service';
import { updateStore } from '../../store/utils';
import { Router } from '@angular/router';

@Component({
  selector: 'app-rest-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

  @ViewChild('sideTabs', {static: false}) sideTabs: TabsetComponent;
  isAuthenticated = false;
  error: string;

  constructor(private authService: AuthService, private store: Store<RestClientState>,
    private loginService: LoginService, private router: Router) {}

  ngOnInit() {
    this.authService.isAuthenticated$.subscribe(isAuth => {
      this.isAuthenticated = isAuth;
    });

    this.store.select(selectError).subscribe(error => {
      this.error = error;
    });

    this.store.select(selectActiveRequest).subscribe(request => {
      if (request && this.sideTabs && this.sideTabs.tabs.length) {
        this.sideTabs.tabs[1].active = true;
      }
    });
  }

  onNewRequest() {
    updateStore(this.store);
    this.router.navigate(['/rest-client']);
  }

  onNewScriptedRequest() {
    updateStore(this.store);
    this.router.navigate(['/rest-client', 'scripted-request']);
  }

  onNewData() {
    updateStore(this.store, {request: {}});
    this.router.navigate(['/rest-client', 'data-editor']);
  }

  onLogin() {
    this.loginService.openDialog();
  }
}
