import {
  ActionReducerMap,
  MetaReducer,
  createSelector,
  createFeatureSelector
} from '@ngrx/store';
import { environment } from '../../../../environments/environment';
import * as requestRoot from './requests';
import * as responseRoot from './response';
import * as savedRequestsRoot from './saved-requests';

export * from './response';
export * from './requests';

export interface RestClientState {
  requestModel: requestRoot.RequestState;
  responseModel: responseRoot.ResponseState;
  savedRequests: savedRequestsRoot.SavedRequestState;
}

export const reducers: ActionReducerMap<RestClientState> = {
  requestModel: requestRoot.requestsReducer,
  responseModel: responseRoot.responseReducer,
  savedRequests: savedRequestsRoot.savedRequestsReducer
};

export const metaReducers: MetaReducer<RestClientState>[] = !environment.production ? [] : [];

// Selectors.

export const selectResponseModel = (state: RestClientState) => state.responseModel.response;
export const selectResponseStatus = (state: RestClientState) => state.responseModel.status;
export const selectResponseLoading = (state: RestClientState) => state.responseModel.loading;
export const selectDynamicPages =  (state: RestClientState) => state.requestModel.requestContentModel.dynamicPages;

export const selectRequest = createFeatureSelector<RestClientState, requestRoot.RequestState>('requestModel');
export const selectRequestModel = createSelector(
  selectRequest,
  (state: requestRoot.RequestState) => state.requestContentModel.request);

export const selectRequestScript = createSelector(
  selectRequest,
  (state: requestRoot.RequestState) => state.requestContentModel.scripts);
