import { Component, OnInit, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';
import { RestClientState } from '../../store';
import { LoadRequests } from '../../store/actions';
import { selectActiveRequest } from '../../store/selectors';
import { RestRequestLink, LinkType } from '../../models';
import { Router, ActivatedRoute } from '@angular/router';
import { ListViewComponent } from 'src/app/shared';

@Component({
  selector: 'app-saved-requests',
  templateUrl: './saved-requests.component.html',
  styles: [`
    i.fa {
      color: #20a8d8
    }
  `]
})
export class SavedRequestsComponent implements OnInit {

  @ViewChild('listView', {static: true}) listView: ListViewComponent;

  requests: RestRequestLink[];
  readonly LinkType = LinkType;
  private selectedRequest: RestRequestLink;

  constructor(private store: Store<RestClientState>, private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {
    this.store.dispatch(new LoadRequests());

    this.store.select((state: RestClientState) => state.savedRequests.requests).subscribe(requests => {
      this.requests = requests;
    });

    this.store.select(selectActiveRequest).subscribe(request => {
      this.selectedRequest = request;
      this.listView.selectItem(request);
    });
  }

  onSelect(item) {
    if (item) {
      const { ownerName, name, linkType } = item;
      if (ownerName && name) {
        if (this.selectedRequest && this.selectedRequest.ownerName === ownerName && this.selectedRequest.name === name) {
          return;
        }
        switch (linkType) {
          case LinkType.RestRequest: this.router.navigate([ownerName, name], {relativeTo: this.route}); break;
          case LinkType.ScriptedRequest: this.router.navigate(['scripted-request', ownerName, name], {relativeTo: this.route}); break;
          case LinkType.DataEditor: this.router.navigate(['data-editor', ownerName, name], {relativeTo: this.route}); break;
        }
      }
    }
  }
}
