import { RestContentModel, ContentTypes } from '../../models';
import { ActionsUnion, ActionTypes, setScripts } from '../actions';
import { RequestsHistoryManager } from '../../requests-history';
import { Variables } from 'src/app/shared/app-variables';

export interface RequestState {
  requestContentModel: RestContentModel;
}

const initRequest = {
  method: 'GET',
  url: '',
  headers: [{
    name: 'Content-Type',
    value: 'application/json',
    active: true
  }],
  queryParameters: [],
  contentType: ContentTypes.JSON,
  body: undefined,
};

const initialRestContentModel: RestContentModel = {
  request: initRequest,
  uiSettings: {
    isRequestPanelCollapsed: true,
    showObjectExplorer: true,
    showContent: true,
    showHeader: true,
    showWorkflow: true,
    showPages: true
  }
};

const initialRestClient: RequestState = {
  requestContentModel: initialRestContentModel
};
/** TODO use createReducer */
export function requestsReducer(state: RequestState = initialRestClient, action: ActionsUnion): RequestState {
  switch (action.type) {
    case ActionTypes.UPDATE_REQUEST_MODEL: {
      const payload = action.payload;
      if (payload) {
        if (payload.headers) {
          const contentType = payload.headers.find(h => h.name === 'Content-Type');
          payload.contentType = contentType ? (contentType.value as ContentTypes) : ContentTypes.JSON;
        }
        if (payload.body && typeof payload.body !== 'string') {
          payload.body = JSON.stringify(payload.body);
        }
      }

      return {
        ...state,
        requestContentModel: {
          ...state.requestContentModel,
          request: payload === undefined ? initRequest : payload
        }
      };
    }

    case setScripts.type: {
      return {
        ...state,
        requestContentModel: {
          ...state.requestContentModel,
          scripts: action.scripts
        }
      };
    }

    case ActionTypes.SAVE_REQUEST: {
      RequestsHistoryManager.saveRequestConfig({
        ...state.requestContentModel,
        responseInfo: action.payload
      });
      return state;
    }

    case ActionTypes.UPDATE_UI_SETTINGS: {
      return {
        ...state,
        requestContentModel: {
          ...state.requestContentModel,
          uiSettings: {...state.requestContentModel.uiSettings, ...action.payload}
        }
      };
    }

    case ActionTypes.SEND_REQUEST: {
      const {url, method} = state.requestContentModel.request;
      Variables.setContextVariable({name: 'requestUrl', value: url});
      Variables.setContextVariable({name: 'requestMethod', value: method});
      return state;
    }

    case ActionTypes.SAVE_DYNAMIC_PAGES: {
      const requestContentModel = {
        ...state.requestContentModel,
        dynamicPages: action.payload
      };
      RequestsHistoryManager.updateRequestConfig(requestContentModel);
      return {
        ...state,
        requestContentModel
      };
    }

    case ActionTypes.SET_DYNAMIC_PAGES: {
      return {
        ...state,
        requestContentModel: {
          ...state.requestContentModel,
          dynamicPages: action.payload || []
        }
      };
    }

    default: {
      return state;
    }
  }
}
