import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { Assertion } from '../../rest-interpreter/models';

@Component({
  selector: 'app-assertion-logs',
  templateUrl: './assertion-logs.component.html',
  styleUrls: ['./assertion-logs.component.scss']
})
export class AssertionLogsComponent implements OnInit, OnChanges {

  @Input() assertions: Assertion[];
  success: Boolean;
  stepsCount: Number;

  constructor() { }

  ngOnInit() {
    this.setState();
  }

  ngOnChanges() {
    this.setState();
  }

  private setState() {
    if (this.assertions) {
      this.success = this.assertions.every(a => a.condition);
      this.stepsCount = this.assertions.length;
    } else {
      this.stepsCount = 0;
    }
  }
}
