import { Component, ViewChild, OnInit, OnDestroy } from '@angular/core';

import { TabSize } from '../../models';
import { RequestComponent } from '../request/request.component';
import { ResponseComponent } from '../response/response.component';
import { Store, select } from '@ngrx/store';
import { RestClientState, selectResponseModel, selectRequestModel } from '../../store';
import { Observable, Subject } from 'rxjs';
import { selectRequestLoading } from '../../store/selectors';
import { RequestModel, UISettings } from '../../models';
import { LocalSettingsService } from 'src/app/shared/services/local-settings.service';
import { takeUntil } from 'rxjs/operators';
import { ScriptContext } from '../../rest-interpreter/models';

@Component({
  selector: 'app-rest-content',
  templateUrl: './rest-content.component.html',
  styles: [`
    :host {
      display: flex;
      flex-direction: column;
      height: 100%;
    }

    as-split.collapsed > ::ng-deep .as-split-gutter {
      display: none;
    }

    as-split.collapsed as-split-area:not(:last-child) {
      margin-bottom: .5rem;
    }

    .spinner {
      top: 0;
      background: white;
      z-index: 8;
    }
  `]
})
export class RestContentComponent implements OnInit, OnDestroy {

  @ViewChild('requestCard', {static: false}) requestCard: RequestComponent;
  @ViewChild('responseCard', {static: false}) responseCard: ResponseComponent;

  uiSettings$: Observable<UISettings>;
  uiSettings: UISettings;
  loading$: Observable<boolean>;
  currentRequest: RequestModel;

  requestTabSize: TabSize = {
    height: 0
  };
  scriptContext: ScriptContext;
  private destroy$ = new Subject();

  constructor(private store: Store<RestClientState>, private localSettings: LocalSettingsService) {}

  ngOnInit() {
    this.uiSettings$ = this.store.pipe(
      takeUntil(this.destroy$),
      select((state => state.requestModel.requestContentModel.uiSettings)));

    this.uiSettings$.subscribe(v => {
      this.uiSettings = v;
      window.requestAnimationFrame(() => {
        this.requestTabSize = this.requestCard.getSize(v.isRequestPanelCollapsed);
      });
    });

    this.store.pipe(
      takeUntil(this.destroy$),
      select(selectRequestModel)).subscribe((req) => {
        this.scriptContext = null;
        this.currentRequest = req;
      });

    this.store.pipe(takeUntil(this.destroy$), select(selectResponseModel)).subscribe(() => {
      window.requestAnimationFrame(() => {
        if (!this.uiSettings.isRequestPanelCollapsed) {
          const panelSizes = this.localSettings.getRequestPanelSize(this.currentRequest);
          if (panelSizes) {
            this.requestTabSize = {...this.requestTabSize, height: panelSizes[0]};
          } else if (this.requestCard) {
            this.requestTabSize = {...this.requestCard.getSize()};
          }
        }
      });
    });

    this.loading$ = this.store.pipe(takeUntil(this.destroy$), select(selectRequestLoading));
  }

  onScriptChange(sc: ScriptContext) {
    this.scriptContext = sc;
  }

  onResize() {
    this.requestCard.resize();
    this.responseCard.resize();
  }

  onDragEnd(evt) {
    this.localSettings.setRequestPanelSize(this.currentRequest, evt.sizes);
    this.onResize();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
