import { Component, Input, EventEmitter, Output, OnInit } from '@angular/core';
import { DialogService } from 'src/app/shared/services';
import { switchMap } from 'rxjs/operators';
import { PageInfo, PageShortInfo } from 'src/app/shared/models';
import { WorkflowConfig, UIModel } from '@ngx-dynamic-components/core';
import { Variables } from 'src/app/shared/app-variables';
import { PageInfoComponent } from 'src/app/shared/components/page-info/page-info.component';
import { getInitialWorkflowConfig } from '../../utils';

@Component({
  selector: 'app-dynamic-page',
  templateUrl: './dynamic-page.component.html',
  styleUrls: ['./dynamic-page.component.scss']
})
export class DynamicPageComponent implements OnInit {

  @Input() config: PageInfo;
  @Input() set dataModel(val: any) {
    if (val) {
      this.data = null;
      this.updatePage({...this.config}).then(() => {
        this.data = typeof val === 'string' ? JSON.parse(val) : val;
      });
    } else {
      this.data = null;
    }
  }
  @Output() changed = new EventEmitter<DynamicPageComponent>();
  page;
  error: string;
  data: any;
  designerMode = false;
  private pageInfo: PageInfo;

  constructor(private dialogService: DialogService) {}

  ngOnInit() {
    this.pageInfo = this.config;
    this.page = this.getPage();
  }

  updatePage(config: PageShortInfo) {
    return new Promise((resolve, reject) => {
      try {
        this.page = null;
        this.error = null;
        window.requestAnimationFrame(() => {
          this.page = this.getPage();
          this.pageInfo = {...this.pageInfo, ...config};
          resolve(this.page);
        });
      } catch (e) {
        console.error(e);
        this.error = e;
        reject(e);
      }
    });
  }

  onUIModelUpdate(uiModel: UIModel) {
    this.pageInfo.uiModel = uiModel;
    this.changed.emit(this);
    this.page = this.getPage();
  }

  editPage() {
    const pageEditDialog = this.dialogService.open(PageInfoComponent, {class: 'modal-xl full-height'});
    pageEditDialog.config = this.pageInfo;
    pageEditDialog.init.pipe(switchMap(() => pageEditDialog.component.save)).subscribe(page => {
      this.updatePage({...page as PageShortInfo}).then(() => {
        this.changed.emit(this);
      });
    });
  }

  designPage() {
    this.designerMode = !this.designerMode;
  }

  onRender({error}) {
    window.requestAnimationFrame(() => {
      this.error = error;
    });
  }

  getData(): PageInfo {
    const vars = {...this.pageInfo.workflows.vars};
    delete vars.uiModel;
    delete vars.dataModel;
    return {
      ...this.pageInfo,
      workflows: {
        failOnError: true,
        include: ['@common'],
        vars,
        workflowsMap: this.pageInfo.workflows.workflowsMap,
        consts: {},
      }
    };
  }

  private getPage(): {workflowConfig: WorkflowConfig, uiModel: UIModel} {
    return {
      uiModel: this.pageInfo.uiModel,
      workflowConfig: getInitialWorkflowConfig({
        variableResolver: Variables,
        ...this.pageInfo.workflows
      })
    };
  }
}
