import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AuthService } from 'src/app/security';

@Injectable({providedIn: 'root'})
export class EnvironmentService {
  constructor(private httpClient: HttpClient, private authService: AuthService) {}

  getEnvVariables$(): Observable<any> {
    return this.httpClient
      .get<any>(`${environment.apiBaseUrl}/environment/variables`, this.authService.getHttpHeaders());
  }

  saveEnvVariables$(vars): Observable<any> {
    return this.httpClient
      .post<any>(`${environment.apiBaseUrl}/environment/variables`, vars, this.authService.getHttpHeaders());
  }
}
