import { Component, Input, ViewChild, ElementRef, AfterViewInit, Output,
  EventEmitter, EmbeddedViewRef, Inject, ComponentFactoryResolver, ApplicationRef,
  Injector, SimpleChanges, OnChanges, OnInit } from '@angular/core';
import { WorkflowConfig, WorkflowEngine, WorkflowEditorComponent } from '@ngx-dynamic-components/core';
import { Ace, edit } from 'ace-builds';
import { formatObjToJsonStr } from 'src/app/shared';
import { Variables } from 'src/app/shared/app-variables';
import { DOCUMENT } from '@angular/common';
import { fromEvent } from 'rxjs';
import { debounceTime, map, filter } from 'rxjs/operators';
import { ACTIONS } from '../../models';
import { getInitialWorkflowConfig } from '../../utils';

@Component({
  selector: 'app-dynamic-workflow',
  template: `
    <alert type="danger" *ngIf="error">
      <strong>Error:</strong> {{error}}
    </alert>
    <div #workflowEl class="h-100 ace-editor"></div>
  `,
  styles: []
})
export class DynamicWorkflowComponent implements AfterViewInit, OnInit, OnChanges {

  @Input() config: WorkflowConfig;
  @Input() set dataModel(val: any) {
    this.data = val;
    this.initializeWorkflow();
  }
  @Output() changed = new EventEmitter<DynamicWorkflowComponent>();
  @ViewChild('workflowEl', {static: false}) workflowEl: ElementRef;

  workflowsMapEdit: WorkflowEditorComponent;
  workflowConfig: WorkflowConfig;
  workflowEditor: Ace.Editor;
  error: string;

  private data: any;
  private workflowEngine: WorkflowEngine;
  private get defaultMap(): WorkflowConfig {
    return getInitialWorkflowConfig({
      workflowsMap: {
        [ACTIONS.PRE_REQUEST]: [],
        [ACTIONS.POST_REQUEST_SUCCESS]: [],
        [ACTIONS.POST_REQUEST_ERROR]: []
      }
    });
  }

  constructor(
    @Inject(DOCUMENT) private document: any,
    private componentFactoryResolver: ComponentFactoryResolver,
    private appRef: ApplicationRef,
    private injector: Injector) {}

  ngOnInit() {
    this.workflowConfig = this.config || this.defaultMap;
    this.initializeWorkflow();
  }

  ngOnChanges({config}: SimpleChanges) {
    if (!config.firstChange && this.workflowConfig !== config.currentValue) {
      this.workflowConfig = config.currentValue || this.defaultMap;
      this.workflowEditor.setValue(formatObjToJsonStr(this.workflowConfig), -1);
      this.initializeWorkflow();
    }
  }

  ngAfterViewInit() {
    this.workflowEditor = edit(this.workflowEl.nativeElement, {
      mode: 'ace/mode/json',
      autoScrollEditorIntoView: true,
      value: formatObjToJsonStr(this.workflowConfig)
    });
    fromEvent(this.workflowEditor, 'change').pipe(
      debounceTime(500),
      map(() => {
        try {
          this.error = null;
          return JSON.parse(this.workflowEditor.getValue());
        } catch (e) {
          this.error = e.message;
          return false;
        }
      }),
      filter(v => Boolean(v))
    ).subscribe((v) => {
      this.workflowConfig = v;
      this.changed.emit(this);
    });
  }

  initializeWorkflow() {
    const vars = {dataModel: this.data};
    const config: WorkflowConfig = {
      ...this.workflowConfig,
      failOnError: true,
      include: ['@common'],
      variableResolver: Variables
    };
    this.workflowEngine = new WorkflowEngine(config);
    this.workflowEngine.hasWorkflow('run').then(init => {
      if (init && this.data) {
        this.workflowEngine.run('run');
      }
    });
  }

  onWorkflowEdit() {
    const componentRef = this.componentFactoryResolver.resolveComponentFactory(WorkflowEditorComponent).create(this.injector);
    this.workflowsMapEdit = componentRef.instance;
    this.workflowsMapEdit.config = this.workflowConfig.workflowsMap;
    this.workflowsMapEdit.modal.onHide.subscribe(() => {
      componentRef.destroy();
    });
    this.workflowsMapEdit.change.subscribe(() => {
      this.workflowEditor.setValue(formatObjToJsonStr(this.workflowConfig), -1);
    });

    this.appRef.attachView(componentRef.hostView);
    const domElem = (componentRef.hostView as EmbeddedViewRef<any>).rootNodes[0] as HTMLElement;

    this.document.body.appendChild(domElem);
    setTimeout(() => {
      this.workflowsMapEdit.openModal();
    });
  }

  async preRequestActions() {
    await this.workflowEngine.run(ACTIONS.PRE_REQUEST);
  }

  resize() {
    this.workflowEditor.resize();
  }

  getData(): WorkflowConfig {
    try {
      return JSON.parse(this.workflowEditor.getValue()) as WorkflowConfig;
    } catch (e) {
      throw Error(`Workflow error: ` + e.message);
    }
  }

  formatJSON(format = true) {
    try {
      this.error = null;
      const workflowsMap = JSON.parse(this.workflowEditor.getValue());
      if (format) {
        this.workflowEditor.setValue(formatObjToJsonStr(workflowsMap), -1);
      } else {
        this.workflowEditor.setValue(JSON.stringify(workflowsMap), -1);
      }
    } catch (e) {
      this.error = e;
    }
  }
}
