import { Component, ViewChild, OnInit, Input } from '@angular/core';
import { Page } from 'src/app/shared';
import { WorkflowEngine, UIModel } from '@ngx-dynamic-components/core';
import { TabsetComponent } from 'ngx-bootstrap';
import { DynamicPageComponent } from '../dynamic-page/dynamic-page.component';
import { Control } from 'wijmo/wijmo';
import { ResponseModel, ResponseStatus, ContentTypes, UISettings } from '../../models';
import { Observable } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { RestClientState, selectResponseModel, selectResponseStatus,
  selectResponseLoading,
  selectDynamicPages } from '../../store';
import { UpdateUiSettings, SaveDynamicPages } from '../../store/actions';
import { PageInfo } from 'src/app/shared/models';
import { Variables } from 'src/app/shared/app-variables';
import { getInitialWorkflowConfig } from '../../utils';
import { ScriptContext } from '../../rest-interpreter/models';

enum PanelContentType {
  WORKFLOW = 'showWorkflow', // deprecated
  PAGE = 'showPages',
  CONTENT = 'showContent',
  HEADER = 'showHeader',
  OBJECT_EXPLORER = 'showObjectExplorer'
}

@Component({
  selector: 'app-response',
  templateUrl: './response.component.html',
  styleUrls: ['./response.component.scss']
})
export class ResponseComponent implements OnInit {

  @Input() scriptContext: ScriptContext;

  loading$: Observable<boolean>;

  responseModel: ResponseModel;
  response$: Observable<ResponseModel>;
  responseStatus$: Observable<ResponseStatus>;
  dynamicPages$: Observable<PageInfo[]>;
  dynamicPages: PageInfo[];
  dataModel: any;
  PanelContentType = PanelContentType;
  panelContentType: PanelContentType = PanelContentType.CONTENT;
  selectedPage: DynamicPageComponent;
  responseType: string;
  workflowEngine: WorkflowEngine;
  uiSettings$: Observable<UISettings>;
  uiSettings: UISettings = {};
  isRefreshDisabled = true;
  objectExplorerUIModel = {
    type: 'rest-client:grid-object-explorer',
    containerProperties: {},
    itemProperties: {
      dataSource: '{{responseContent}}',
      host: '{{requestMethod}}-{{requestUrl}}',
      isReadOnly: false,
      allowDelete: true,
      allowAddNew: true,
      selectionMode: 4
    }
  };

  pageUiModel = Page.uiModel;

  tabsOptions = [];

  constructor(private store: Store<RestClientState>) {}

  updateTabsOptions() {
    const options = [
      {value: 'showContent', title: 'Show content tab'},
      {value: 'showHeader', title: 'Show header tab'},
      {value: 'showObjectExplorer', title: 'Show Object Explorer'},
    ];

    this.tabsOptions = options;
  }

  ngOnInit() {
    this.response$ = this.store.pipe(select(selectResponseModel));
    this.dynamicPages$ = this.store.pipe(select(selectDynamicPages));
    this.responseStatus$ = this.store.pipe(select(selectResponseStatus));
    this.loading$ = this.store.pipe(select(selectResponseLoading));

    this.response$.subscribe(responseModel => {
      this.responseModel = responseModel;
      window.requestAnimationFrame(() => {
        this.updateTabsOptions();
        this.updateSelectedTab();
      });
      this.setContentValue(responseModel);
    });

    this.uiSettings$ = this.store.pipe(
      select((state => state.requestModel.requestContentModel.uiSettings)));
    this.uiSettings$.subscribe(v => {
      this.uiSettings = v;
      this.updateSelectedTab();
    });

    this.dynamicPages$.subscribe(dynamicPages => {
      this.dynamicPages = dynamicPages || [];
      this.updateTabsOptions();
    });
  }

  updateSelectedTab() {
    if (!this.uiSettings[this.panelContentType] ||
      this.panelContentType === PanelContentType.HEADER && !this.responseModel.headers) {

      this.panelContentType = null;
    }
  }

  get tabsStateClasses() {
    return {
      'show-content': this.uiSettings.showContent,
      'show-header': this.uiSettings.showHeader && this.responseModel.headers,
      'show-explorer': this.uiSettings.showObjectExplorer,
      'show-page': this.uiSettings.showPages,
      'show-workflow': this.uiSettings.showWorkflow,
    };
  }

  onTabsOptionsChanged(e) {
    this.uiSettings[e.target.value] = e.target.checked;
    this.store.dispatch(new UpdateUiSettings(this.uiSettings));
  }

  onAddDynamicPage() {
    const length = 1;
    const workflowIndex = this.dynamicPages.length + 1;
    this.dynamicPages.push({
      title: 'Page ' + workflowIndex,
      description: 'Dynamic page ' + workflowIndex,
      pageName: `page${workflowIndex}`,
      uiModel: {id: `page${workflowIndex}`, ...this.pageUiModel} as UIModel,
      workflows: getInitialWorkflowConfig({workflowsMap: { [`page${workflowIndex}_OnInit`]: []}})
    });
    window.requestAnimationFrame(() => {
      this.store.dispatch(new SaveDynamicPages(this.dynamicPages));
      this.updateTabsOptions();
      this.editCurrentPage();
    });
  }

  onDynamicWorkfowChanged() {
    this.isRefreshDisabled = false;
  }

  onPageSelect(tabElementRef, i: number) {
    Control.invalidateAll(tabElementRef);
    this.selectedPage = null;
  }

  onObjectExplorerSelect() {
    this.panelContentType = PanelContentType.OBJECT_EXPLORER;
    this.selectedPage = null;
  }

  onContentSelect() {
    this.panelContentType = PanelContentType.CONTENT;
  }

  // TODO: implement show in presenter resize.
  resize() {

  }

  removePage(index: number) {
    this.dynamicPages.splice(index, 1);
    window.requestAnimationFrame(() => {
      this.store.dispatch(new SaveDynamicPages(this.dynamicPages));
      this.updateTabsOptions();
    });
  }

  editCurrentPage() {
    this.selectedPage.editPage();
  }

  designCurrentPage() {
    this.selectedPage.designPage();
  }

  trackPageFn(page, index: number) {
    return `dynamic-component-${index}`;
  }

  private setContentValue(responseModel: ResponseModel) {
    const value = Variables.resolveString('{{responseContent}}');
    if (!value || value === '{{responseContent}}') {
      this.dataModel = null;
      this.responseType = null;
    } else {
      if (responseModel.contentType === ContentTypes.JSON && typeof value === 'object') {
        this.responseType = 'json';
      } else {
        if ((value as string).toLowerCase().startsWith('<!doctype html')) {
          this.responseType = 'html';
        } else {
          this.responseType = 'text';
        }
      }
      this.dataModel = value;
    }
  }
}
