import { getInterpreter } from 'src/app/shared/interpreter';
import { Interpreter } from 'src/app/shared/interpreter/interpreter';
import { getErrorMessage } from 'src/app/shared';
import { ScriptContext, ShowInTypes } from './models';
import './functions';
import { Variables } from 'src/app/shared/app-variables';

export class RestInterpreter {
  scriptContext: ScriptContext;
  interpreter: Interpreter;
  constructor() {
    this.reset();

    this.interpreter = getInterpreter();

    this.interpreter.addFunction('assert', (condition: boolean, name?: string, description?: string) => {
      this.scriptContext.asserts = [...this.scriptContext.asserts, { condition, name, description }];
    });

    this.interpreter.addFunction('error', (error: string) => {
      this.scriptContext.errors = [...this.scriptContext.errors, error];
    });

    this.interpreter.addFunction('showInObjectExplorer', (data: any, title?: string) => {
      this.showIn(ShowInTypes.ObjectExplorer, data, title);
    });

    this.interpreter.addFunction('showInJSON', (data: any, title: any) => {
      this.showIn(ShowInTypes.JSON, data, title);
    });

    this.interpreter.addFunction('plotChart', (data: any, title: any, chartConfig: any) => {
      this.showIn(ShowInTypes.Chart, data, title, chartConfig);
    });
  }

  async evaluate(script: string, context: object = null, entryFunctionName: string = null): Promise<any> {
    try {
      const res = await this.interpreter.evaluate(script, {...context}, entryFunctionName);
      Variables.syncAppVariables();
      return res;
    } catch (e) {
      this.scriptContext.errors = [...this.scriptContext.errors, getErrorMessage(e)];
    }
  }

  reset() {
    this.scriptContext = {
      errors: [],
      asserts: [],
      showInItems: []
    };
  }

  private showIn(type: string, data: any, title?: string, config?: any) {
    this.scriptContext.showInItems = [...this.scriptContext.showInItems, { type, data, title, config }];
  }


}
