import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { getDomain, deepClone, ListViewComponent } from 'src/app/shared';
import { RequestsHistoryManager } from '../../requests-history';
import { HistoryRestContentModel } from '../../models';
import { RestClientState } from '../../store';
import { UpdateUiSettings } from '../../store/actions';
import { getScriptName } from '../../utils';
import { updateStore } from '../../store/utils';

@Component({
  selector: 'app-history',
  templateUrl: './history.component.html',
  styleUrls: ['./history.component.scss']
})
export class HistoryComponent implements OnInit {

  @ViewChild('list', {static: false}) list: ListViewComponent;

  requests: HistoryRestContentModel[] = [];

  groupBy = {
    groupDate: true,
    url: false
  };
  grouping = this.dateGrouping;

  showTime = false;
  showFullUrl = false;

  getScriptName = getScriptName;

  constructor(private store: Store<RestClientState>, private router: Router) {}

  ngOnInit() {
    RequestsHistoryManager.onRequestSave((req, isNew) => {
      const newItem = deepClone(req);
      if (isNew) {
        this.requests = [...this.requests, newItem];
        this.list.selectItem(newItem);
      } else {
        const index = this.requests.findIndex((item: HistoryRestContentModel) => {
          return RequestsHistoryManager.equals(item, newItem);
        });
        this.requests = [...this.requests.slice(0, index), newItem, ...this.requests.slice(index + 1)];
        this.list.selectItem(newItem);
      }
    });

    this.requests = RequestsHistoryManager.getSavedRequests() || [];
  }

  onDateGroupClick() {
    if (this.groupBy.groupDate) {
      this.groupBy.url = false;
      this.grouping = this.dateGrouping;
    } else {
      this.grouping = null;
    }
  }

  onDomainGroupClick() {
    if (this.groupBy.url) {
      this.groupBy.groupDate = false;
      this.grouping = this.domainGrouping;
    } else {
      this.grouping = null;
    }
  }

  onSelect(item: HistoryRestContentModel) {
    if (!item) {
      return null;
    }
    if (item.hasOwnProperty('request')) {
      const content = deepClone(item);
      this.store.dispatch(new UpdateUiSettings(content.uiSettings));
      updateStore(this.store, content, '/rest-client');
    } else if (item.scripts) {
      updateStore(this.store, { scripts: item.scripts }, '/rest-client/scripted-request');
    }
  }

  searchHitoryItem(str: string, item: HistoryRestContentModel) {
    return [item.request.method, item.request.url, item.responseInfo.status].join().toLowerCase().includes(str);
  }

  getDisplayUrl(url: string) {
    if (!url || url.length < 26) {
      return url;
    }
    return this.showFullUrl ? url : ('...' + url.slice(-25));
  }

  dateGrouping(item: HistoryRestContentModel) {
    return new Date(item.responseInfo.date).toDateString();
  }

  domainGrouping(item: HistoryRestContentModel) {
    return item.request ? getDomain(item.request.url) : 'script';
  }
}
