import { ActionConfig } from '@ngx-dynamic-components/core';

export * from './rest-content-model';

export interface TabSize {
  height: number | '*';
  minHeight?: number;
  maxHeight?: number;
  isCollapsed?: boolean;
}

export interface ResponseError {
  name: string;
  message: string;
  status: number;
  statusText: string;
  time?: number;
}

export enum ACTIONS {
  PRE_REQUEST = 'pre-request-actions',
  POST_REQUEST = 'post-request-actions',
  POST_REQUEST_SUCCESS = 'success-response-actions',
  POST_REQUEST_ERROR = 'error-response-actions'
}

export enum INTERPRETER {
  PRE_REQUEST = 'preRequest',
  POST_REQUEST_SUCCESS = 'successResponse',
  POST_REQUEST_ERROR = 'errorResponse'
}

export interface RunRequestConfig extends ActionConfig {
  requestName: string;
  assertions?: any[];
  ignoreSuccessResponseActions?: boolean;
  ignoreErrorResponseActions?: boolean;
  setVariables?: any;
  addAssertionsTo?: string;
}
