import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RestLayoutComponent } from './components/rest-layout/rest-layout.component';
import { RestContentComponent } from './components/rest-content/rest-content.component';
import { ScriptedRequestComponent } from './components/scripted-request/scripted-request.component';
import { DataEditorComponent } from './components/data-editor/data-editor.component';

const routes: Routes = [{
  path: '',
  component: RestLayoutComponent,
  children: [{
    path: '',
    component: RestContentComponent,
    children: [{
      path: ':ownerName/:requestName'
    }]
  }, {
    path: 'scripted-request',
    component: ScriptedRequestComponent,
    children: [{
      path: ':ownerName/:requestName'
    }]
  }, {
    path: 'data-editor',
    component: DataEditorComponent,
    children: [{
      path: ':ownerName/:requestName'
    }]
  }]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RestClientRoutingModule { }
