import { Component, OnInit, Output, EventEmitter, Input, ViewChild, ElementRef } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import * as curl from 'parse-curl';
import { RestContentModel, ContentTypes, RequestModel } from '../../models';
import { Variables } from 'src/app/shared/app-variables';
import { getInitialWorkflowConfig } from '../../utils';

@Component({
  selector: 'app-curl-form',
  templateUrl: './curl-form.component.html',
  styleUrls: ['./curl-form.component.scss']
})
export class CurlFormComponent implements OnInit {

  @Output() import = new EventEmitter<RestContentModel>();
  @Input() modalRef: ModalDirective;
  @Input() exportData: RequestModel;
  @ViewChild('txtArea', {static: true}) txtArea: ElementRef<HTMLTextAreaElement>;
  cUrl: string;
  isExport = false;
  error: string;

  get title() {
    return this.isExport ? 'Export to cUrl' : 'Import from cUrl';
  }

  ngOnInit() {
    if (this.isExport && this.exportData) {
      const resolved = Variables.resolve(this.exportData);
      const {method, url, body, headers} = resolved as RequestModel;
      let bodyParam = '';
      let headersParam = '';

      if (method === 'POST' && body) {
        if (this.exportData.contentType !== ContentTypes.JSON) {
          const params = Object.entries(JSON.parse(body)).reduce((prev, [key, val]) => {
            prev.push(`${key}=${val}`);
            return prev;
          }, []).join('&');
          bodyParam = `-d "${params}"`;
        } else {
          bodyParam = `-d '${body}'`;
        }
      }

      if (headers) {
        headersParam = headers.filter(h => h.active).map(h => {
          return `-H "${h.name}: ${h.value}"`;
        }).join(' ');
      }

      this.cUrl = `curl -X ${method} ${url} ${bodyParam} ${headersParam}`.replace(/[\n\t]/g, '');
    }
  }

  importCurl() {
    const curlData = curl(this.cUrl);

    if (curlData) {
      let headers = [];

      if (curlData.header) {
        headers = Object.entries(curlData.header).map(([name, value]) => {
          return {name, value, active: true};
        });
      }

      const model: RestContentModel = {
        request: {
          method: curlData.method,
          url: curlData.url,
          headers,
          queryParameters: [],
          contentType: null
        }
      };

      if (curlData.body) {
        if (headers.some(h => h.name === 'Content-Type' && h.value === ContentTypes.JSON)) {
          model.request.body = curlData.body.replace(/\t|\n/g, '');
          model.request.contentType = ContentTypes.JSON;
        } else {
          model.request.body = this.parseQueryStr(curlData.body);
          model.request.contentType = ContentTypes.FORM;
        }
      }
      this.import.emit(model);
      this.modalRef.hide();
    } else {
      this.error = 'Not valid curl';
    }
  }

  copy() {
    this.txtArea.nativeElement.select();
    document.execCommand('copy');
    this.modalRef.hide();
  }

  private parseQueryStr(str: string) {
    return JSON.stringify(str.split('&').reduce((prev, kv) => {
      const [key, value] = kv.split('=');
      prev[key] = value;
      return prev;
    }, {}));
  }

}
