import { Component, OnInit, ViewChild, ElementRef, Input } from '@angular/core';
import { ScriptContext } from '../../rest-interpreter/models';
import { RestInterpreter } from '../../rest-interpreter';
import { snippets } from '../../rest-interpreter/snippets';
import { Ace, edit } from 'ace-builds';


@Component({
  selector: 'app-script',
  templateUrl: './script.component.html',
  styleUrls: ['./script.component.scss']
})
export class ScriptComponent implements OnInit {

  @Input() defaultScript: string;
  @ViewChild('script', { static: true }) scriptEl: ElementRef;

  private editor: Ace.Editor;
  restInterpreter: RestInterpreter;
  evaluationTime: number;
  snippets = snippets;
  get context(): ScriptContext {
    return this.restInterpreter.scriptContext;
  }

  constructor() { }

  ngOnInit() {
    this.restInterpreter = new RestInterpreter();
    this.editor = edit(this.scriptEl.nativeElement, {
      mode: 'ace/mode/python',
      autoScrollEditorIntoView: true,
      value: this.defaultScript,
      tabSize: 2,
      useSoftTabs: true,
      indentedSoftWrap: true
    });

    this.editor.setOptions({
      enableBasicAutocompletion: true,
      enableSnippets: false,
      enableLiveAutocompletion: true
    });
  }

  getScript(): string {
    return this.editor.getValue();
  }

  async evaluate(): Promise<any> {
    this.evaluationTime = null;
    this.restInterpreter.reset();
    const startTime = new Date().getTime();
    const script = this.getScript();
    const res = await this.restInterpreter.evaluate(script);
    this.evaluationTime = +new Date() - startTime;
    return res;
  }

  resize() {
    this.editor.resize();
  }

  setScript(script: string) {
    this.evaluationTime = null;
    if (this.editor) {
      this.setValue(script);
    } else {
      window.requestAnimationFrame(() => {
        this.setValue(script);
      });
    }
  }

  addSnippet(snippet: string) {
    const row = this.editor.session.getLength() - 1;
    const column = this.editor.session.getLine(row).length;
    this.editor.moveCursorTo(row, column);
    this.editor.insert(`\n${snippet}`, true);
  }

  private setValue(script: string) {
    this.editor.setValue(script, -1);
    this.restInterpreter.reset();
  }
}
