import { RestRequestLink } from '../../models';
import { SavedRequestsActionTypes, SavedRequestsActionsUnion } from '../actions';

export interface SavedRequestState {
  saving: boolean;
  requests: RestRequestLink[];
  activeRequest: RestRequestLink;
  error: string;
  requestLoading: boolean;
}

const initialState: SavedRequestState = {
  requests: [],
  saving: false,
  activeRequest: null,
  error: null,
  requestLoading: false
};

export function savedRequestsReducer(state: SavedRequestState = initialState, action: SavedRequestsActionsUnion): SavedRequestState {
  switch (action.type) {
    case SavedRequestsActionTypes.SAVE_REQUEST: {
      return {
        ...state,
        saving: true
      };
    }

    case SavedRequestsActionTypes.SAVED_REQUEST: {
      const index = state.requests.findIndex(r => r.id === action.payload.id);
      if (index > -1) {
        state.requests[index] = action.payload;
      } else {
        const { id, folder, ownerName, name, description, linkType } = action.payload;
        state.activeRequest = { id, folder, ownerName, name, description, linkType };
        state.requests.push(action.payload);
      }
      return {
        ...state,
        requests: [...state.requests]
      };
    }

    case SavedRequestsActionTypes.LOADED_REQUESTS: {
      return {
        ...state,
        requests: action.payload,
        error: null
      };
    }

    case SavedRequestsActionTypes.LOAD_REQUESTS_FAILED: {
      return {
        ...state,
        error: action.payload
      };
    }

    case SavedRequestsActionTypes.LOADED_REQUEST: {
      const { id, folder, ownerName, name, description, linkType, scripts } = action.payload;
      return {
        ...state,
        activeRequest: { id, folder, ownerName, name, description, linkType },
        requestLoading: false
      };
    }

    case SavedRequestsActionTypes.DESELECT_REQUEST: {
      return {
        ...state,
        activeRequest: null
      };
    }

    case SavedRequestsActionTypes.LOAD_REQUEST: {
      return {
        ...state,
        requestLoading: true
      };
    }

    case SavedRequestsActionTypes.LOAD_REQUEST_FAILED: {
      return {
        ...state,
        requestLoading: false
      };
    }

    case SavedRequestsActionTypes.DELETED_REQUEST: {
      const index = state.requests.findIndex(r => r.id === action.payload);
      if (index === -1) {
        return state;
      }
      return {
        ...state,
        requests: [...state.requests.slice(0, index), ...state.requests.slice(index + 1)]
      };
    }

    default: return state;
  }
}
