import { Injectable } from '@angular/core';
import { switchMap, map, catchError } from 'rxjs/operators';
import { of, from } from 'rxjs';
import { RequestsService } from './requests.service';
import { setResponseContextVars, getResponse } from '../utils';
import { RestInterpreter } from '../rest-interpreter';
import { registerFunction } from 'src/app/shared/interpreter';
import { RestRequestInfo, INTERPRETER } from '../models';

@Injectable({
  providedIn: 'root'
})
export class RunRequestService {
  constructor(private requestService: RequestsService) {
    function executeRequest(requestName: string, ignorePreRequestScripts = true, ignorePostRequestScripts = true) {
      const [ownerName, objectName] = requestName.split('.');
      return requestService.getSavedRequest$({ownerName, requestName: objectName}).pipe(
        switchMap(((requestInfo: RestRequestInfo) => {
          if (requestInfo) {
            return requestService.send(requestInfo.request).pipe(
              catchError(e => of(e)),
              map((response: any) => ({response, requestInfo})),
              switchMap(({response}) => {
                const res = getResponse(response);
                setResponseContextVars(res);
                if (!ignorePostRequestScripts) {
                  const script = requestInfo.scripts;
                  if (response.status === 200) {
                    return from(new RestInterpreter().evaluate(script, {response: res}, INTERPRETER.POST_REQUEST_SUCCESS)).pipe(
                      map(() => res));
                  } else {
                    return from(new RestInterpreter().evaluate(script, {error: res}, INTERPRETER.POST_REQUEST_ERROR)).pipe(
                      map(() => res));
                  }
                }
                return of(response);
              }));
          }
          return of (null);
        }))).toPromise();
    }
    registerFunction('runRequest', executeRequest);
  }
}
