import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Store, select } from '@ngrx/store';
import { Observable, Subject, merge } from 'rxjs';
import { switchMap, takeUntil } from 'rxjs/operators';
import { RestClientState, selectResponseModel, selectRequest, RequestState } from '../../store';
import { selectRequestLoading, selectActiveRequest } from '../../store/selectors';
import { ResponseModel, RestRequestLink, LinkType, UISettings } from '../../models';
import { ContentExplorerComponent, dateToDtoString } from 'src/app/shared';
import { SaveRequestComponent } from '../save-request/save-request.component';
import { DialogService } from 'src/app/shared/services';
import { SaveRequest, DeleteRequest } from '../../store/actions';
import { LoginService, AuthService } from 'src/app/security/services';
import { updateStore } from '../../store/utils';
import { ShowInPresenterComponent } from '../show-in-presenter/show-in-presenter.component';
import { ScriptComponent } from '../script/script.component';
import { RequestsHistoryManager } from '../../requests-history';

@Component({
  selector: 'app-scripted-request',
  templateUrl: './scripted-request.component.html',
  styleUrls: ['./scripted-request.component.scss']
})
export class ScriptedRequestComponent implements OnInit, OnDestroy {

  loading$: Observable<any>;
  response$: Observable<ResponseModel>;
  responseData: any;
  requestState: RequestState;
  isAuthenticated: boolean;
  @ViewChild('responseExplorer', {static: false}) responseExplorer: ContentExplorerComponent;
  @ViewChild('script', {static: true}) script: ScriptComponent;
  @ViewChild('showIn', { static: false }) showIn: ShowInPresenterComponent;
  saveRequestLink: RestRequestLink;
  selectedSavedRequestLink: RestRequestLink;
  uiSettings$: Observable<UISettings>;
  uiSettings: UISettings;
  defaultScript = [
    '# script 1'
  ].join('\n');
  private runningScript$ = new Subject<boolean>();
  private destroy = new Subject();

  readonly responseUIModel = {
    type: 'rest-client:grid-object-explorer',
    containerProperties: {},
    itemProperties: {
      dataSource: null,
      isReadOnly: false,
      allowDelete: true,
      allowAddNew: true,
      host: 'response-body-object-explorer',
      selectionMode: 4
    }
  };

  readonly newRequestLink: RestRequestLink = {
    ownerName: null,
    name: null,
    folder: null,
    description: null,
    linkType: LinkType.ScriptedRequest,
    id: 0
  };

  constructor(
    private store: Store<RestClientState>,
    private dialogService: DialogService,
    private authService: AuthService,
    private loginService: LoginService,
    private router: Router) { }

  ngOnInit() {
    this.response$ = this.store.pipe(takeUntil(this.destroy), select(selectResponseModel));
    this.loading$ = merge(this.store.select(selectRequestLoading), this.runningScript$);

    this.response$.subscribe(r => {
      this.responseData = r.body;
    });

    this.store.pipe(takeUntil(this.destroy), select(selectRequest)).subscribe(data => {
      this.requestState = data;
      const script = data.requestContentModel.scripts || this.defaultScript;
      this.script.setScript(script);
    });

    this.authService.isAuthenticated$.subscribe(isAuth => {
      this.isAuthenticated = isAuth;
      if (isAuth) {
        const userName = this.authService.user.userName;
        this.newRequestLink.ownerName = userName;
        if (this.saveRequestLink && this.saveRequestLink.id === 0) {
          this.saveRequestLink.ownerName = userName;
        }
      }
    });

    this.store.pipe(takeUntil(this.destroy),
      select(selectActiveRequest)).subscribe(request => {
      this.selectedSavedRequestLink = request;
      this.saveRequestLink = request || {...this.newRequestLink};
    });
  }

  ngOnDestroy() {
    this.destroy.next();
    this.destroy.complete();
  }

  async run() {
    this.runningScript$.next(true);
    await this.script.evaluate();
    this.runningScript$.next(false);
    RequestsHistoryManager.saveRequestConfig({
      scripts: this.script.getScript(),
      responseInfo: {
        date: dateToDtoString(new Date()),
        time: this.script.evaluationTime,
        status: null,
        statusText: null
      }});
  }

  save() {
    if (this.isAuthenticated) {
      const saveDialog = this.dialogService.open(SaveRequestComponent);
      saveDialog.config = this.saveRequestLink;
      saveDialog.init.pipe(
        switchMap(() => saveDialog.component.save)).subscribe(data => {
          this.store.dispatch(new SaveRequest({
            ...data as RestRequestLink,
            scripts: this.script.getScript()
          }));
          saveDialog.component.modalRef.hide();
      });
    } else {
      this.loginService.openDialog();
    }
  }

  onDeleteRequest() {
    if (this.selectedSavedRequestLink) {
      this.store.dispatch(new DeleteRequest(this.selectedSavedRequestLink.id));
    } else {
      throw new Error('Saved request is not selected');
    }
  }

  onNewScriptedRequest() {
    updateStore(this.store);
    this.router.navigate(['/rest-client', 'scripted-request']);
  }

  onResize() {
    this.script.resize();
    if (this.responseExplorer) {
      this.responseExplorer.resize();
    }
    if (this.showIn) {
      this.showIn.resize();
    }
  }
}
