import { Component, OnInit, ViewChildren, QueryList, Input, SimpleChanges, OnChanges } from '@angular/core';
import { UIModel } from '@ngx-dynamic-components/core';
import { ContentExplorerComponent } from 'src/app/shared';
import { ShowInItem, ShowInTypes } from '../../rest-interpreter/models';
import { ChartType, Position, SelectionMode } from 'wijmo/wijmo.chart';
import { ChartProperties } from 'src/app/wijmo/components/chart/chart.component';

interface ShowInExplorerItem extends ShowInItem {
  uModel: UIModel;
}

@Component({
  selector: 'app-show-in-presenter',
  templateUrl: './show-in-presenter.component.html',
  styleUrls: ['./show-in-presenter.component.scss']
})
export class ShowInPresenterComponent implements OnInit, OnChanges {

  @Input() items: ShowInItem[];
  @ViewChildren(ContentExplorerComponent) contentExplorers: QueryList<ContentExplorerComponent>;
  tabs: Array<ShowInItem | ShowInExplorerItem>;
  objectExplorerUIModels = [];
  ShowInTypes = ShowInTypes;
  constructor() { }

  ngOnInit() {
    this.tabs = this.getTabsList(this.items);
  }

  ngOnChanges({ items }: SimpleChanges) {
    if (!items.firstChange && items.currentValue) {
      this.tabs = this.getTabsList(items.currentValue);
    }
  }

  private getTabsList(items: ShowInItem[]): Array<ShowInItem | ShowInExplorerItem> {
    if (!items) {
      return null;
    }
    return items.map((item, i) => {
      if (item.type === ShowInTypes.ObjectExplorer) {
        return {
          ...item,
          uiModel: this.getObjectExplorerUIModel(`${item.type}-${item.title}-${i}`)
        };
      } else if (item.type === ShowInTypes.Chart) {
        return {
          ...item,
          uiModel: this.getChartUIModel(item)
        };
      }
      return item;
    });
  }

  resize() {
    this.contentExplorers.toArray().forEach(ce => {
      ce.resize();
    });
  }

  private getObjectExplorerUIModel(host: string) {
    return {
      type: 'rest-client:grid-object-explorer',
      containerProperties: {},
      itemProperties: {
        host,
        isReadOnly: true,
        allowDelete: false,
        allowAddNew: false,
        selectionMode: 4
      }
    };
  }

  private getChartUIModel(item: ShowInItem) {
    item.config = (item.config || {}) as ChartProperties;
    let header = item.config.header;
    let bindingX = item.config.bindingX;
    let series = item.config.series || [];
    if (item.data && !Array.isArray(item.data)) {
      item.data = [item.data];
    }
    if (item.data.length) {
      const keys = Object.keys(item.data[0]);

      if (!header) {
        header = keys[0];
      }

      if (!bindingX) {
        bindingX = keys[0];
      }

      if (!series.length) {
        series = Object.entries(item.data[0]).map(([key, value]) => {
          if (typeof value === 'number') {
            return { binding: key, name: key };
          }
        }).filter(v => v);
      }
    }

    return {
      type: 'wijmo:chart',
      containerProperties: {},
      itemProperties: {
        chartType: item.config.chartType || ChartType.Line,
        header,
        bindingX,
        selectionMode: item.config.selectionMode || SelectionMode.Point,
        legendPosition: item.config.legendPosition || Position.Bottom,
        series: series,
        dataSource: item.data
      }
    };
  }
}
