import { Component, ElementRef, ViewChild, Output, EventEmitter, OnInit, OnDestroy, Input } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { TabsetComponent } from 'ngx-bootstrap';
import { Observable, Subject } from 'rxjs';
import { takeUntil, switchMap } from 'rxjs/operators';
import { LoginService, AuthService } from 'src/app/security/services';
import { getDomain, ContentExplorerComponent } from 'src/app/shared';
import { DialogService, FileManagerService } from 'src/app/shared/services';
import { RequestModel, RestRequestLink, RestContentModel, LinkType, TabSize } from '../../models';
import { selectRequestModel, RestClientState, selectRequest, RequestState, selectRequestScript } from '../../store';
import { updateStore } from '../../store/utils';
import { UpdateRequestModel, SendRequest, UpdateUiSettings, SaveRequest, DeleteRequest,
  ClearResponse, setScripts, RecieveResponse, SaveRequestInHistory } from '../../store/actions';
import { selectActiveRequest } from '../../store/selectors';
import { CurlFormComponent } from '../curl-form/curl-form.component';
import { filterDynamicPagesVars } from '../../utils';
import { SaveRequestComponent } from '../save-request/save-request.component';
import { RequestsService } from '../../services/requests.service';
import { ScriptContext } from '../../rest-interpreter/models';
import { ScriptComponent } from '../script/script.component';

@Component({
  selector: 'app-request',
  templateUrl: './request.component.html',
  styleUrls: ['./request.component.scss'],
  providers: [FileManagerService]
})
export class RequestComponent implements OnInit, OnDestroy {

  @Output() collapse = new EventEmitter<TabSize>();
  @Output() scriptResult = new EventEmitter<ScriptContext>();
  @ViewChild('bodyEditor', {static: true}) bodyEditor: ContentExplorerComponent;
  @ViewChild('header', {static: false}) header: ElementRef;
  @ViewChild('requestTabs', {static: true}) requestTabs: TabsetComponent;
  @ViewChild('script', {static: true}) script: ScriptComponent;

  readonly minCardBodyHeight = 90; // 230
  readonly newRequestLink: RestRequestLink = {
    ownerName: null,
    name: null,
    folder: null,
    description: null,
    linkType: LinkType.RestRequest
  };
  readonly explorerUIModel = {
    type: 'rest-client:grid-object-explorer',
    containerProperties: {},
    itemProperties: {
      dataSource: null,
      isReadOnly: false,
      allowDelete: true,
      allowAddNew: true,
      host: 'request-body-object-explorer',
      selectionMode: 4
    }
  };
  requestModel: RequestModel;
  request$: Observable<RequestModel>;
  bodyData: any;
  isCollapsed$: Observable<boolean>;
  isCollapsed = true;
  error: string;
  exportData: RequestState;
  selectedSavedRequestLink: RestRequestLink;
  saveRequestLink: RestRequestLink = {...this.newRequestLink};
  isAuthenticated = false;
  formatted = false;

  defaultScript = [
    'def preRequest(request):',
    '  request.cancelRequest = False\n',
    'def successResponse(response):',
    '  showInObjectExplorer(response.body, "Content")',
    '  showInJSON(response.headers, "Header")\n',
    'def errorResponse(response):',
    '  showInObjectExplorer(response.body, "Error")',
    '  showInJSON(response.headers, "Header")'].join('\n');

  readonly methods = ['GET', 'POST', 'PUT', 'DELETE', 'OPTIONS'];
  private destroy$ = new Subject();

  constructor(
    public authService: AuthService,
    private store: Store<RestClientState>,
    private fileManager: FileManagerService,
    private loginService: LoginService,
    private dialogService: DialogService,
    private requestsService: RequestsService) { }

  ngOnInit() {
    this.request$ = this.store.pipe(
      takeUntil(this.destroy$),
      select(selectRequestModel));

    this.authService.isAuthenticated$.subscribe(isAuth => {
      this.isAuthenticated = isAuth;
      if (isAuth) {
        const userName = this.authService.user.userName;
        this.saveRequestLink.ownerName = userName;
        this.newRequestLink.ownerName = userName;
      }
    });

    this.request$.subscribe(requestModel => {
      this.requestModel = requestModel;
      this.bodyData = requestModel.body;
      if (this.bodyEditor) {
        this.bodyEditor.setReadOnly(this.requestModel.method === 'GET');
      }
      this.syncUrl();
    });

    this.isCollapsed$ = this.store.pipe(
      takeUntil(this.destroy$),
      select((state => state.requestModel.requestContentModel.uiSettings.isRequestPanelCollapsed)));
    this.isCollapsed$.subscribe(v => this.isCollapsed = v);

    this.store.pipe(takeUntil(this.destroy$), select(selectRequest)).subscribe(data => {
      this.exportData = data;
    });
    this.store.pipe(takeUntil(this.destroy$), select(selectRequestScript)).subscribe(script => {
      this.script.setScript(script || this.defaultScript);
    });
    this.store.pipe(takeUntil(this.destroy$), select(selectActiveRequest)).subscribe(request => {
      this.selectedSavedRequestLink = request;
      this.saveRequestLink = request || {...this.newRequestLink};
    });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  get isFormatActionActive() {
    return this.requestTabs.tabs[0].active && this.requestModel.method !== 'GET' || this.requestTabs.tabs[3].active;
  }

  onUrlInput() {
    this.syncUrl();
    this.updateModel();
  }

  formatJSON(format = true) {
    try {
      if (this.requestTabs.tabs[0].active) {
        this.bodyEditor.formatJSON(format);
      }
      this.formatted = format;
      this.error = null;
    } catch (e) {
      console.error(e);
      this.error = e;
      this.formatted = false;
    }
  }

  updateModel() {
    this.error = null;
    this.store.dispatch(new UpdateRequestModel(this.requestModel));
  }

  isRequestValid(): boolean {
    if (!this.requestModel.body) {
      return true;
    }
    try {
      return this.bodyEditor.isValid();
    } catch (e) {
      this.error = e.message;
      return false;
    }
  }

  selectMethod(method: string) {
    this.requestModel.method = method;
    this.updateModel();
    this.bodyEditor.setReadOnly(method === 'GET');
    this.bodyData = method === 'GET' ? undefined : {};
  }

  async sendRequest() {
    if (this.isRequestValid()) {
      if (this.requestModel.method !== 'GET' && this.bodyEditor.dataModel) {
        this.requestModel.body = JSON.stringify(this.bodyEditor.dataModel);
      }
      this.store.dispatch(new ClearResponse());
      const scripts = this.script.getScript();
      this.store.dispatch(setScripts({scripts}));
      this.store.dispatch(new SendRequest(this.requestModel));
      this.script.restInterpreter.reset();
      const sendStatus = await this.requestsService.sendRequest(this.requestModel, scripts, this.script.restInterpreter);
      this.scriptResult.emit(this.script.context);
      this.store.dispatch(new RecieveResponse(sendStatus.response, sendStatus.status));
      this.store.dispatch(new SaveRequestInHistory(sendStatus.status));
    }
  }

  getSize(isCollapsed = false) {
    const headerH = this.header.nativeElement.offsetHeight;
    const minHeight = this.minCardBodyHeight + headerH;
    const defaultHeight = minHeight + 200;
    return {
      height: isCollapsed ? headerH : defaultHeight,
      minHeight
    };
  }

  toggleCard() {
    this.store.dispatch(new UpdateUiSettings({
      isRequestPanelCollapsed: !this.isCollapsed
    }));
  }

  addQueryParam() {
    this.requestModel.queryParameters.push({name: '', value: '', active: true});
    this.syncQueryParams();
    this.updateModel();
  }

  deleteQueryParam(index: number) {
    this.requestModel.queryParameters.splice(index, 1);
    this.syncQueryParams();
    this.updateModel();
  }

  addHeader() {
    this.requestModel.headers.push({name: '', value: '', active: true});
    this.updateModel();
  }

  deleteHeader(index: number) {
    this.requestModel.headers.splice(index, 1);
    this.updateModel();
  }

  onBodyTabSelect() {
    window.requestAnimationFrame(() => {
      this.bodyEditor.setReadOnly(this.requestModel.method === 'GET');
      this.bodyEditor.resize();
    });
  }

  onNewRequest() {
    updateStore(this.store);
  }

  onDeleteRequest() {
    if (this.selectedSavedRequestLink) {
      this.store.dispatch(new DeleteRequest(this.selectedSavedRequestLink.id));
    } else {
      throw new Error('Saved request is not selected');
    }
  }

  onFileExport() {
    const domain = getDomain(this.exportData.requestContentModel.request.url);
    this.fileManager.exportToFile({requestContentModel: filterDynamicPagesVars(this.exportData.requestContentModel)}, {
      fileName: `rc-${domain}`
    });
  }

  onFileImport() {
    this.fileManager.importFromFile().subscribe(({result}: {result: RequestState}) => {
      const requestContentModel = result.requestContentModel;
      updateStore(this.store, requestContentModel);
    }, error => {
      this.error = error.message;
      this.store.dispatch(new UpdateUiSettings({isRequestPanelCollapsed: false}));
    });
  }

  onCurlImport() {
    const importCurlDialog = this.dialogService.open(CurlFormComponent);
    importCurlDialog.init.pipe(
      switchMap(() => importCurlDialog.component.import)).subscribe(data => {
        updateStore(this.store, data as RestContentModel);
      });
  }

  onCurlExport() {
    const exportCurlDialog = this.dialogService.open(CurlFormComponent);
    exportCurlDialog.init.pipe().subscribe(() => {
      exportCurlDialog.component.isExport = true;
      exportCurlDialog.component.exportData = this.exportData.requestContentModel.request;
    });
  }

  onSave() {
    if (this.isAuthenticated) {
      const saveDialog = this.dialogService.open(SaveRequestComponent);
      saveDialog.config = this.saveRequestLink;
      saveDialog.init.pipe(
        switchMap(() => saveDialog.component.save)).subscribe(data => {
          this.store.dispatch(new SaveRequest({
            ...this.exportData.requestContentModel,
            ...data as RestRequestLink,
            scripts: this.script.getScript()
          }));
          saveDialog.component.modalRef.hide();
      });
    } else {
      this.loginService.openDialog();
    }
  }

  resize() {
    this.bodyEditor.resize();
    this.script.resize();
  }

  private syncUrl() {
    if (this.requestModel.url) {
      const urlQuery = this.requestModel.url.split('?')[1];
      this.requestModel.queryParameters = [];
      if (urlQuery) {
        urlQuery.split('&').forEach(qp => {
          const [name, value] = qp.split('=');
          this.requestModel.queryParameters.push({ active: true, name, value: value ? decodeURIComponent(value) : null});
        });
      }
    }
  }

  private syncQueryParams() {
    const url = this.requestModel.url.split('?')[0];
    const queryStr = this.requestModel.queryParameters.filter(qp => qp.active && qp.name).map(qp => {
      return `${qp.name}=${encodeURIComponent(qp.value as string)}`;
    }).join('&');
    this.requestModel.url = url + (queryStr ? `?${queryStr}` : '');
  }
}
