import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { catchError, switchMap, map } from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/services/notification.service';
import { NotificationType } from 'src/app/shared/models';
import { getErrorMessage } from 'src/app/shared';
import { SavedRequestsActionTypes, AppErrorAction, AppInfoAction, AppActionTypes } from '../actions/index';
import { RequestsService } from '../../services/requests.service';
import { RestRequestLink, RestRequestInfo, OwnerRequest, LinkType } from '../../models';
import { LoadedRequests, LoadRequestsFailed, SavedRequest,
  SaveRequestFailed, LoadedRequest, DeselectRequest, LoadRequestFailed, DeletedRequest } from '../actions/saved-requests';
import { UpdateRequestModel, UpdateUiSettings, ClearResponse, SetDynamicPages, setScripts } from '../actions/rest-content';
@Injectable()
export class RestClientEffects {
  constructor(
    private actions$: Actions,
    private requestsService: RequestsService,
    private router: Router,
    private notificationService: NotificationService) {}

  @Effect()
  loadRequests = this.actions$.pipe(
    ofType(SavedRequestsActionTypes.LOAD_REQUESTS),
    switchMap(() => this.requestsService.getSavedRequests$()),
    map((requests) => new LoadedRequests(requests)),
    catchError(e => of(new LoadRequestsFailed(e.error || 'Failed to load saved requests.'))));

  @Effect()
  saveRequest = this.actions$.pipe(
    ofType(SavedRequestsActionTypes.SAVE_REQUEST),
    switchMap(({payload}) => {
      return this.requestsService.saveRequest$(payload).pipe(
        switchMap(id => {
          const link = payload as RestRequestLink;
          // TODO: remove hardcoded module path.
          switch (link.linkType) {
            case LinkType.RestRequest: this.router.navigate(['/rest-client', link.ownerName, link.name]); break;
            case LinkType.ScriptedRequest: this.router.navigate(['/rest-client/scripted-request', link.ownerName, link.name]); break;
            case LinkType.DataEditor: this.router.navigate(['/rest-client/data-editor', link.ownerName, link.name]); break;
          }

          return [
            new SavedRequest({id, ...link}),
            new AppInfoAction('Request saved successfully')
          ];
        }),
        catchError(e => of(new AppErrorAction(getErrorMessage(e, `Failed to save request ${payload['name']}`)))));
    }),
    catchError(e => of(new SaveRequestFailed(e.error))));

  @Effect()
  loadRequest = this.actions$.pipe(
    ofType(SavedRequestsActionTypes.LOAD_REQUEST),
    switchMap(({payload}) => {
      const { ownerName, requestName } = payload as OwnerRequest;
      if (!ownerName || !requestName) {
        return [new DeselectRequest()];
      }
      return this.requestsService.getSavedRequest$(payload).pipe(
        switchMap((requestInfo: RestRequestInfo) => {
          if (!requestInfo) {
            const error = `Request ${requestName} of owner ${ownerName} not found`;
            return [
              new AppErrorAction(error),
              new DeselectRequest(),
              new LoadRequestFailed(error),
            ];
          }
          return [
            new LoadedRequest(requestInfo),
            new UpdateRequestModel(requestInfo.request),
            new SetDynamicPages(requestInfo.dynamicPages),
            new UpdateUiSettings(requestInfo.uiSettings),
            setScripts({scripts: requestInfo.scripts}),
            new ClearResponse()
          ];
        }),
        catchError(e => of(
          new LoadRequestFailed(e.error || `Failed to load ${requestName} of owner ${ownerName}`),
          new AppErrorAction(e.error || `Failed to load ${requestName} of owner ${ownerName}`)
        )));
    }));

  @Effect()
  deleteRequest = this.actions$.pipe(
    ofType(SavedRequestsActionTypes.DELETE_REQUEST),
    switchMap(({payload}) => this.requestsService.deleteRequest$(payload).pipe(
      switchMap(() => {
        return [
          new DeletedRequest(payload),
          new DeselectRequest(),
          new AppInfoAction('Request deleted')
        ];
      }),
      catchError(e => of(new AppErrorAction(e.error || `Failed to delete request id: ${payload}.`)))
    )));

  @Effect()
  appError = this.actions$.pipe(
    ofType(AppActionTypes.ERROR),
    map(({payload}) => {
      this.notificationService.open({
        type: NotificationType.DANGER,
        message: payload
      });
      return {type: '[Rest client] error shown'};
    }));

  @Effect()
  appInfo = this.actions$.pipe(
    ofType(AppActionTypes.INFO),
    map(({payload}) => {
      this.notificationService.open({
        type: NotificationType.SUCCESS,
        message: payload
      });
      return {type: '[Rest client] info shown'};
    }));

  @Effect()
  deselectRequest = this.actions$.pipe(
    ofType(SavedRequestsActionTypes.DESELECT_REQUEST),
    map(({payload}) => {
      if (payload) {
        this.router.navigateByUrl(payload);
      } else if (this.router.url.includes('/rest-client/scripted-request')) {
        this.router.navigate(['/rest-client/scripted-request']);
      } else if (this.router.url.includes('/rest-client/data-editor')) {
        this.router.navigate(['/rest-client/data-editor']);
      } else {
        this.router.navigate(['/rest-client']);
      }
      return {type: '[Saved requests] deselected'};
    }));
}
