import { Variables } from 'src/app/shared/app-variables';
import { registerFunction } from 'src/app/shared/interpreter';

registerFunction('setAppVariable', (name: string, value: any) => {
  Variables.setVariable(name, value);
});

registerFunction('getAppVariable', (name: string) => {
  return Variables.variables[name] !== undefined ? Variables.variables[name] : null;
});

registerFunction('setContextVariable', (name: string, value: any) => {
  Variables.setContextVariable({ name, value });
});

registerFunction('getContextVariable', (name: string) => {
  return Variables.getContextVar(name) !== undefined ? Variables.getContextVar(name) : null;
});
