import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RestRequestLink } from '../../models';
import { ModalDirective } from 'ngx-bootstrap';
import { deepClone } from 'src/app/shared';

@Component({
  selector: 'app-save-request',
  templateUrl: './save-request.component.html',
  styleUrls: ['./save-request.component.scss']
})
export class SaveRequestComponent implements OnInit {

  @Input() config: RestRequestLink;
  @Input() modalRef: ModalDirective;
  @Output() save = new EventEmitter<RestRequestLink>();

  saveRequestLink: RestRequestLink;

  ngOnInit() {
    this.saveRequestLink = deepClone(this.config);
  }

  saveRequest() {
    this.save.emit(this.saveRequestLink);
  }

}
