import { Action, createAction, props } from '@ngrx/store';
import { RequestModel, ResponseStatus, UISettings } from '../../models';
import { PageInfo } from 'src/app/shared/models';

export enum ActionTypes {
  UPDATE_REQUEST_MODEL = '[Rest content] update model',
  SEND_REQUEST = '[Rest content] send request',
  RECEIVE_RESPONSE = '[Rest content] receive responce',
  UPDATE_RESPONSE_WORKFLOWS = '[Rest content] update response workflows',
  SAVE_REQUEST = '[Rest content] save request to history',
  CLEAR_RESPONSE = '[Rest content] clear response',
  SET_RESPONSE_WORKFLOWS = '[Rest content] set response workflows',
  SAVE_RESPONSE_WORKFLOWS = '[Rest content] save response workflows',
  UPDATE_UI_SETTINGS = '[Rest content] update ui settings',
  SET_DYNAMIC_PAGES = '[Rest content] set dynamic pages',
  SAVE_DYNAMIC_PAGES = '[Rest content] save dynamic pages'
}

export const setScripts = createAction('[Rest content] set scripts', props<{scripts: string}>());

export class UpdateRequestModel implements Action {
  readonly type = ActionTypes.UPDATE_REQUEST_MODEL;

  constructor(public payload?: RequestModel) {}
}

export class SendRequest implements Action {
  readonly type = ActionTypes.SEND_REQUEST;

  constructor(public payload: RequestModel) {}
}

export class SaveRequestInHistory implements Action {
  readonly type = ActionTypes.SAVE_REQUEST;
  constructor(public payload: ResponseStatus) {}
}

export class RecieveResponse implements Action {
  readonly type = ActionTypes.RECEIVE_RESPONSE;

  constructor(public payload?, public status?: ResponseStatus) {}
}

export class SetDynamicPages implements Action {
  readonly type = ActionTypes.SET_DYNAMIC_PAGES;

  constructor(public payload?: PageInfo[]) {}
}

export class SaveDynamicPages implements Action {
  readonly type = ActionTypes.SAVE_DYNAMIC_PAGES;

  constructor(public payload: PageInfo[]) {}
}

export class ClearResponse implements Action {
  readonly type = ActionTypes.CLEAR_RESPONSE;
}

export class UpdateUiSettings implements Action {
  readonly type = ActionTypes.UPDATE_UI_SETTINGS;

  constructor(public payload: UISettings) {}
}

export type ActionsUnion = SendRequest | UpdateRequestModel |
  RecieveResponse | SaveRequestInHistory |
  ClearResponse | UpdateUiSettings | SaveDynamicPages | SetDynamicPages | any;
