import { RestContentModel } from '../models';
import { UpdateRequestModel, DeselectRequest, ClearResponse, SetDynamicPages, setScripts } from './actions';
import { Store } from '@ngrx/store';
import { RestClientState } from './reducers';

export function updateStore(store: Store<RestClientState>, requestContentModel?: RestContentModel<any>, url?: string) {
  const model = requestContentModel || { request: undefined };
  store.dispatch(new UpdateRequestModel(model.request));
  store.dispatch(new SetDynamicPages(model.dynamicPages));
  store.dispatch(new DeselectRequest(url));
  store.dispatch(setScripts({ scripts: model.scripts }));
  store.dispatch(new ClearResponse());
}
