import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { throwError } from 'rxjs';
import { switchMap, map, catchError } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { BsModalRef, BsModalService, ModalDirective } from 'ngx-bootstrap';

import { Variables } from 'src/app/shared/app-variables';
import { AuthService } from 'src/app/security';
import { NameValue } from '../../models';
import { EnvironmentService } from '../../services/environment.service';
import { RestClientState } from '../../store';
import { AppInfoAction, AppErrorAction } from '../../store/actions';
import { deepClone } from 'src/app/shared';

@Component({
  selector: 'app-environments',
  templateUrl: './environments.component.html',
  styleUrls: ['./environments.component.scss']
})
export class EnvironmentsComponent implements OnInit {
  readonly newEnvironment = {
    name: null,
    variables: [
      { name: '', value: null }
    ]
  };
  modalRef: BsModalRef;
  environment: { name: string; variables: NameValue[] };
  environments = [];
  envs: {};
  editableEnvs: {};
  variables = [{ name: '', value: null }];
  localVariables = [];
  envVariables = [];
  selectedEnvironment: string = null;
  formSelectedEnv: string;
  newEnv: boolean;
  isAuthenticated = false;
  @ViewChild('envTemplate', { static: false }) envTemplate: TemplateRef<any>;
  @ViewChild('appVarTemplate', { static: false }) appVarTemplate: TemplateRef<any>;
  @ViewChild('modal', { static: false }) public modal: ModalDirective;

  constructor(private modalService: BsModalService,
    private envService: EnvironmentService,
    private authService: AuthService,
    private store: Store<RestClientState>) { }

  ngOnInit() {
    this.authService.isAuthenticated$.subscribe(isAuth => {
      this.isAuthenticated = isAuth;
      if (isAuth) {
        this.loadEnvVariables$().subscribe(() => {
          if (this.environments.length) {
            if (this.environments.includes(Variables.currentEnv)) {
              this.onEnvSelect(Variables.currentEnv);
            } else {
              this.onEnvSelect(this.environments[0]);
            }
            this.modalService.onHide.subscribe(() => this.resetEnvForm());
          }
        });
      } else {
        this.envs = {};
        this.environments = [];
      }
    });
  }

  onEnvSelect(env: string) {
    this.selectedEnvironment = env;
    const variables = Object.entries(this.envs[this.selectedEnvironment]).map(([name, value]) => ({ name, value }));
    Variables.addVariables(variables);
    Variables.currentEnv = this.selectedEnvironment;
  }

  onAppVarEnvChange(evt) {
    this.onEnvSelect(evt.target.value);
    this.setAppVariables();
  }

  editEnv() {
    if (!this.selectedEnvironment) {
      this.setNewEnv();
    } else {
      this.formSelectedEnv = this.selectedEnvironment;
      this.setSelectEnv();
    }

    this.modalRef = this.modalService.show(this.envTemplate, { class: 'modal-lg' });
  }

  setNewEnv() {
    this.newEnv = true;
    this.formSelectedEnv = null;
    this.cleanUpEnvFormVars();
  }

  setSelectEnv() {
    if (this.formSelectedEnv) {
      this.environment = {
        name: this.formSelectedEnv,
        variables: Object.entries(this.editableEnvs[this.formSelectedEnv]).map(([name, value]) => ({ name, value }))
      };
    } else {
      this.cleanUpEnvFormVars();
    }
    this.newEnv = false;
  }


  editAppVar() {
    this.setAppVariables();
    this.modalRef = this.modalService.show(this.appVarTemplate, { class: 'modal-lg' });
  }

  deleteEnv() {
    this.editableEnvs = this.envs;
    const envToDelete = this.formSelectedEnv;
    delete this.editableEnvs[envToDelete];
    this.saveEnvs$().subscribe(() => {
      this.store.dispatch(new AppInfoAction(`Environment ${envToDelete} successfully deleted`));
      if (this.selectedEnvironment === envToDelete) {
        this.selectedEnvironment = null;
        Variables.currentEnv = null;
      }
    }, () => {
      this.store.dispatch(new AppErrorAction('Failed to delete environment'));
    });
  }

  onEnvSubmit() {
    if (this.newEnv) {
      this.editableEnvs[this.environment.name] = this.environment.variables.reduce((prev, val) => {
        prev[val.name] = val.value;
        return prev;
      }, {});
      this.formSelectedEnv = this.environment.name;
    }

    const newProps = this.environment.variables.map(({ name }) => name);

    // Add new fields to all envs.
    Object.entries(this.editableEnvs).forEach(([key, value]) => {
      const keys = Object.keys(value);
      newProps.forEach(prop => {
        if (!keys.includes(prop)) {
          value[prop] = null;
        }
      });
    });
    this.saveEnvs$(this.formSelectedEnv).subscribe(() => {
      if (this.modalRef) {
        this.modalRef.hide();
      }
      this.store.dispatch(new AppInfoAction('Environment changes successfully updated'));
      Variables.currentEnv = this.selectedEnvironment;
    }, (e) => {
      this.store.dispatch(new AppErrorAction('Failed to save environment'));
    });
  }

  onEnvVarNameChanged(evt: string, name: string) {
    Object.entries(this.editableEnvs).forEach(([key, value]) => {
      const val = value[name];
      value[evt] = val || null;
      delete value[name];
    });
  }

  onEnvFormChange() {
    this.setSelectEnv();
  }

  onEnvVarValueChanged(evt: string, name: string) {
    if (this.formSelectedEnv) {
      this.editableEnvs[this.formSelectedEnv][name] = evt;
    }
  }

  onAppVarSubmit() {
    Variables.setVariables([]);
    Variables.addVariables(this.envVariables);
    Variables.addVariables(this.localVariables);
    this.modalRef.hide();
  }

  deleteAppVariable(index: number) {
    this.localVariables.splice(index, 1);
  }

  addAppVariable() {
    this.localVariables.push({ name: '', value: '' });
  }

  deleteVariable(index: number, name: string) {
    Object.entries(this.editableEnvs).forEach(([key, value]) => {
      delete value[name];
    });
    this.environment.variables.splice(index, 1);
  }

  addVariable() {
    this.environment.variables.push({ name: '', value: '' });
  }

  private resetEnvForm() {
    this.editableEnvs = deepClone(this.envs);
  }

  private loadEnvVariables$() {
    return this.envService.getEnvVariables$().pipe(map(envs => {
      this.envs = envs;
      this.resetEnvForm();
      this.environments = envs ? Object.keys(envs) : [];
      this.setNewEnv();
      if (!this.environments.includes(this.selectedEnvironment)) {
        this.selectedEnvironment = null;
      }

      if (!this.environments.includes(this.formSelectedEnv)) {
        this.formSelectedEnv = null;
      }
      return envs;
    }));
  }

  private saveEnvs$(newEnv?: string) {
    return this.envService.saveEnvVariables$({ EnvironmentVariables: this.editableEnvs }).pipe(
      switchMap(() => {
        return this.loadEnvVariables$().pipe(
          map(() => {
            if (newEnv) {
              this.onEnvSelect(newEnv);
            }
          })
        );
      }),
      catchError(() => {
        return throwError('Failed to save environment');
      }));
  }

  private cleanUpEnvFormVars() {
    if (this.environments.length) {
      this.newEnvironment.variables = Object.keys(this.editableEnvs[this.environments[0]]).map(name => ({ name, value: null }));
    }
    this.environment = { ...this.newEnvironment };
  }

  private getEnvKeys() {
    if (this.environments.length) {
      return Object.keys(Object.values(this.envs)[0]);
    }
    return [];
  }

  private setAppVariables() {
    this.variables = Object.entries(Variables.variables).map(([name, value]) => ({ name, value }));
    if (!this.variables.length) {
      this.variables = [{ name: '', value: null }];
    }

    this.localVariables = this.variables.filter(e => !this.getEnvKeys().includes(e.name));
    this.envVariables = this.variables.filter(e => this.getEnvKeys().includes(e.name));
  }
}
