import { HttpResponse } from '@angular/common/http';
import { ContentTypes, ResponseModel, RestContentModel } from './models';
import { Variables } from 'src/app/shared/app-variables';
import { WorkflowConfig } from '@ngx-dynamic-components/core';

export function setResponseContextVars(response: ResponseModel) {
  Variables.setContextVariable({name: 'responseContent', value: response.body});
  Variables.setContextVariable({name: 'responseHeaders', value: response.headers});
}

export function getResponse(response: any): ResponseModel {
  return {
    headers: getHeaders(response),
    ...getFormattedResponse(response)
  };
}

function getFormattedResponse(response): {contentType: ContentTypes, body: string | object } {
  const jsonChars = '{[';
  let body = response.body || response.error || '';

  if (typeof body !== 'string') {
    body = JSON.stringify(body);
  }

  if (!body) {
    return {
      contentType: ContentTypes.TEXT,
      body
    };
  }
  if (jsonChars.includes(body[0])) {
    try {
      body = JSON.parse(body);
      return {
        contentType: ContentTypes.JSON,
        body
      };
    } catch {
      return {
        contentType: ContentTypes.TEXT,
        body
      };
    }
  }

  return {
    contentType: ContentTypes.TEXT,
    body
  };
}

function getHeaders(res: HttpResponse<any>): object {
  return Object.entries(res).filter(([key]) => key !== 'body').reduce((prev, [key, val]) => {
    prev[key] = key === 'headers' ? getResponseHeaders(res) : val;
    return prev;
  }, {});
}

function getResponseHeaders(res: HttpResponse<any>): object[] {
  if (res && res.headers) {
    const keys = res.headers.keys();
    const headers = keys.reduce((prev, key) => {
      prev.push({
        active: true,
        name: key,
        value: res.headers.get(key)
      });
      return prev;
    }, []);
    return headers.map(v => {
      return {[v.name]: v.value};
    });
  }
  return [];
}

export function getInitialWorkflowConfig(workflowProps = {}): WorkflowConfig {
  return {
    failOnError: true,
    include: ['@common'],
    vars: {},
    workflowsMap: {},
    consts: {},
    ...workflowProps
  };
}

export function filterDynamicPagesVars(content: RestContentModel) {
  if (content.dynamicPages) {
    content.dynamicPages = content.dynamicPages.map(page => {
      const vars = {...page.workflows.vars};
      delete vars.uiModel;
      delete vars.dataModel;
      page.workflows.vars = vars;
      return page;
    });
  }
  return content;
}

export function getScriptName(scripts: string): string {
  if (!scripts) {
    return null;
  }

  const firstLine = scripts.split('\n')[0];
  const name = firstLine.match(/#([\w\s]+)/);

  if (name && name.length) {
    return name[1].trim();
  }
}
