export * from './rest-content';
export * from './saved-requests';

import { Action } from '@ngrx/store';

export enum AppActionTypes {
  ERROR = '[Rest client] error appear',
  INFO = '[Rest client] info'
}

export class AppErrorAction implements Action {
  readonly type = AppActionTypes.ERROR;

  constructor(public payload) {}
}

export class AppInfoAction implements Action {
  readonly type = AppActionTypes.INFO;

  constructor(public payload) {}
}

export type AppActionsUnion = AppErrorAction | AppInfoAction;
