import { ContentTypes, ResponseModel, ResponseStatus } from '../../models';
import { ActionsUnion, ActionTypes } from '../actions';
import { setResponseContextVars } from '../../utils';

export interface ResponseState {
  response: ResponseModel;
  status: ResponseStatus;
  loading: boolean;
}

const initResponse: ResponseModel = {
  contentType: ContentTypes.JSON,
  body: '',
  headers: null
};

const initResponseState = {
  response: initResponse,
  status: null,
  loading: false
};

export function responseReducer(state: ResponseState = initResponseState, action: ActionsUnion): ResponseState {
  switch (action.type) {

    case ActionTypes.RECEIVE_RESPONSE: {
      if (!action.payload) {
        return { ...initResponseState };
      }
      const response = action.payload;
      return {
        response,
        status: action.status,
        loading: false
      };
    }

    case ActionTypes.CLEAR_RESPONSE: {
      setResponseContextVars({...initResponse});
      return {
        ...state,
        response: {...initResponse},
        status: null
      };
    }

    case ActionTypes.SEND_REQUEST: {
      return {
        ...state,
        loading: true
      };
    }

    default: {
      return state;
    }
  }
}
