import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders, HttpParams } from '@angular/common/http';
import { last, catchError, switchMap, first, tap, map } from 'rxjs/operators';
import { RequestModel, RestRequestInfo, RestRequestLink, OwnerRequest, INTERPRETER, ResponseStatus, SendRequestStatus } from '../models';
import { Variables } from 'src/app/shared/app-variables';
import { Observable, throwError, of, from } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AuthService } from 'src/app/security';
import { Store } from '@ngrx/store';
import { RestClientState, selectRequest } from '../store';
import { WorkflowConfig, WorkflowEngine } from '@ngx-dynamic-components/core';
import { filterDynamicPagesVars, getResponse, setResponseContextVars } from '../utils';
import { RestInterpreter } from '../rest-interpreter';
import { ScriptContext } from '../rest-interpreter/models';
import { dateToDtoString } from 'src/app/shared';

@Injectable({
    providedIn: 'root'
})
export class RequestsService {

  constructor(private httpClient: HttpClient, private authService: AuthService, private store: Store<RestClientState>) {}

  /*
  * TODO: Add return type
  */
  send(requestModel: RequestModel): Observable<any> {
    const { method, url, headers, queryParameters, body } = Variables.resolve<RequestModel>(requestModel);
    let httpHeader = new HttpHeaders();

    headers.forEach(({name, value}) => {
      httpHeader = httpHeader.set(name, value as string);
    });

    let httpParams = new HttpParams();
    queryParameters.forEach(({name, value}) => {
      httpParams = httpParams.append(name, value as string);
    });

    const req = new HttpRequest(method as any, url.replace(/\/+/g, '/').replace(':/', '://'), body, {
      headers: httpHeader,
      params: httpParams,
      responseType: 'text',
    });

    return this.httpClient.request(req).pipe(last());
  }

  getSavedRequests$(): Observable<RestRequestLink[]> {
    return this.httpClient
      .get<RestRequestLink[]>(`${environment.apiBaseUrl}/my/requests`, this.authService.getHttpHeaders());
  }

  getSavedRequest$({ownerName, requestName}: OwnerRequest): Observable<RestRequestInfo> {
    return this.httpClient
      .get<RestRequestInfo>(`${environment.apiBaseUrl}/my/request/${ownerName}/${requestName}`, this.authService.getHttpHeaders())
      .pipe(catchError(() => throwError('Failed to load.')));
  }

  saveRequest$(rrInfo: RestRequestInfo): Observable<number> {
    return this.httpClient
      .post<number>(`${environment.apiBaseUrl}/my/request`, filterDynamicPagesVars(rrInfo), this.authService.getHttpHeaders());
  }

  deleteRequest$(requestId: number): Observable<any> {
    return this.httpClient
      .delete<any>(`${environment.apiBaseUrl}/my/request/${requestId}`, this.authService.getHttpHeaders());
  }

  async executeInterpreter(script: string, funcName: string, context: any = {}, interpreter = new RestInterpreter()):
    Promise<RestInterpreter> {
    if (script) {
      await interpreter.evaluate(script, {
        ...context
      }, funcName);
      return interpreter;
    }
    return Promise.resolve(null);
  }

  sendRequest(request: RequestModel, script: string, interpreter: RestInterpreter): Promise<SendRequestStatus> {
    const startTime = new Date().getTime();
    return from(this.executeInterpreter(script, INTERPRETER.PRE_REQUEST, { request }, interpreter)).pipe(
      switchMap((eI) => {
        if (request.cancelRequest) {
          return of({scriptContext: eI.scriptContext});
        }
        return this.send(request).pipe(
          switchMap((response) => responseHandler(response, startTime, eI.scriptContext)),
          catchError(error => responseHandler(error, startTime, eI.scriptContext)),
          switchMap(({response, status }) => from(
            this.executeInterpreter(script, status.status === 200 ? INTERPRETER.POST_REQUEST_SUCCESS : INTERPRETER.POST_REQUEST_ERROR,
              { response }, eI)).pipe(
              map((e: RestInterpreter) => {
                setResponseContextVars(response);
                return {
                  response,
                  status,
                  scriptContext: e.scriptContext
                };
              })
          )));
      }),
      catchError(e => of({scriptContext: e.scriptContext}))).toPromise();
  }
}

function responseHandler(response: any, startTime: number, scriptContext: ScriptContext): Observable<SendRequestStatus> {
  const time = +new Date() - startTime;
  const status: ResponseStatus = {
    status: response.status,
    statusText: response.statusText,
    time,
    date: dateToDtoString(new Date())
  };

  if (response.status !== 200 && response.message) {
    status.error = response.message;
  }

  const res = getResponse(response);
  return of({response: res, status, scriptContext});
}
