import { Component, OnInit, OnDestroy } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router, NavigationEnd } from '@angular/router';
import { Subject, combineLatest, of } from 'rxjs';
import { filter, takeUntil, catchError, switchMap } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { RestClientState } from '../../store';
import { LoadRequest, DeselectRequest, LoadedRequests } from '../../store/actions';
import { OwnerRequest } from '../../models';
import { AuthService } from 'src/app/security';
import { LayoutConfig } from 'src/app/shared/models';
import { RunRequestService } from '../../services/run-request.service';

@Component({
  selector: 'app-rest-layout',
  templateUrl: './rest-layout.component.html',
  styleUrls: ['./rest-layout.component.scss']
})
export class RestLayoutComponent implements OnInit, OnDestroy {

  layoutConfig: LayoutConfig = {
    icon: 'assets/img/rc-logo-64x64.png',
    smIcon: 'assets/img/rc-logo-36x36.png',
    appName: 'rest-client'
  };

  private destroy = new Subject<any>();

  constructor(
    private titleService: Title,
    private store: Store<RestClientState>,
    private router: Router,
    private authService: AuthService,
    private actions: RunRequestService) {
      combineLatest([
        this.router.events.pipe(takeUntil(this.destroy), filter(event => event instanceof NavigationEnd)),
        this.authService.getCurrentUser().pipe(
          catchError(e => of(false)),
          switchMap(user => {
            if (user) {
              return of(user);
            }
            return this.authService.isAuthenticated$;
          }))
      ]).subscribe(([route, isAuth]) => {
        if (!this.router.routerState.snapshot.url.startsWith('/rest-client') || !isAuth) {
          return;
        }
        let params: OwnerRequest;
        let firstChild = this.router.routerState.root.firstChild;
        while (firstChild) {
          params = firstChild.snapshot.params as OwnerRequest;
          firstChild = firstChild.firstChild;
        }
        if (params.ownerName && params.requestName) {
          this.store.dispatch(new LoadRequest(params));
        } else {
          this.store.dispatch(new DeselectRequest());
        }
      });
      this.authService.getCurrentUser().pipe(
        catchError(e => of(false)),
        filter(isAuth => !isAuth)).subscribe(() => {
        this.store.dispatch(new LoadedRequests([]));
        this.store.dispatch(new DeselectRequest());
      });
    }

  ngOnInit() {
    this.titleService.setTitle('Worksheets REST Client');
  }

  ngOnDestroy() {
    this.destroy.next();
    this.destroy.complete();
  }
}
